import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import { charterSelector } from '../../../../../../../store/reducers/charterSlice/charterSlice';
import { positionSelector } from '../../../../../../../store/reducers/positionSlice/positionSlice';
import { WeeksModeCharter } from '../WeeksModeCharter/WeeksModeCharter';
import { WeeksModePosition } from '../WeeksModePosition/WeeksModePosition';

interface WeekModeStageChildProps {
    childGuid: string;
}
export const WeekModeStageChild: FC<WeekModeStageChildProps> = ({ childGuid }) => {
    const charters = useSelector(charterSelector.selectEntities);
    const positions = useSelector(positionSelector.selectEntities);

    const chartersGuids = useSelector(charterSelector.selectIds);

    const isCharter = chartersGuids.includes(childGuid);
    return (
        <>
            {isCharter ? (
                <WeeksModeCharter charter={charters[childGuid]} />
            ) : (
                <WeeksModePosition position={positions[childGuid]} />
            )}
        </>
    );
};
