import React, { FC } from 'react';
import cls from './WeeksModeStage.module.css';
import classNames from 'classnames';
import moment from 'moment';
import { Stage } from '../../../../../../../store/reducers/stageSlice/stageSlice.types';
import { StageItem } from '../../../../../../UI/StageItem/StageItem';

interface WeekItemStageProps {
    week: { title: string; days: string[]; date: string };
    isPlanFact: boolean;
    stage: Stage;
}
export const WeekItemStage: FC<WeekItemStageProps> = ({ stage, week, isPlanFact }) => {
    const stageDate = moment(stage.planned.start_date).format('DD.MM.YYYY');

    const nowDate = moment().format('DD.MM.YYYY');

    const isRenderDays =
        week.date === moment(stage.planned.start_date).format('MM.YYYY') ||
        week.date === moment().format('MM.YYYY');

    return (
        <div className={classNames(cls.week, { [cls.factWrapper]: isPlanFact })}>
            {isRenderDays &&
                week.days.map((day) => (
                    <div
                        key={day + Math.random().toString(36)}
                        data-id={day}
                        className={classNames(cls.day, {
                            [cls.factWrapper]: isPlanFact,
                            [cls.nowDate]: day === nowDate,
                        })}
                    >
                        {stageDate === day && (
                            <StageItem isPlanFact={isPlanFact} id={`${stage.id}`} stage={stage} />
                        )}
                    </div>
                ))}
        </div>
    );
};
