import { Stage } from '../store/reducers/stageSlice/stageSlice.types';
import { normalize, schema } from 'normalizr';
import { Charter } from '../store/reducers/charterSlice/charterSlice.types';

export const getNormalizedStages = (stagesData: Stage[]) => {
    const arrayPosition: any[] = [];

    for (const stage of stagesData) {
        const positions = Object.values(stage.positions);
        const updatedStage = { ...stage, positions };
        arrayPosition.push(updatedStage);
    }

    const position = new schema.Entity(
        'positions',
        {},
        {
            idAttribute: 'guid',
        }
    );
    const stage = new schema.Entity('stages', {
        positions: [position],
    });

    const normalizedData = normalize(arrayPosition, [stage]);

    return normalizedData;
};

export const getNormalizedCharters = (chartersData: Charter[]) => {
    const arrayChildren: any[] = [];

    for (const charter of chartersData) {
        const children = Object.values(charter.children);
        const updatedCharter = { ...charter, children };
        arrayChildren.push(updatedCharter);
    }

    const child = new schema.Entity('children', {}, { idAttribute: 'guid' });
    const charter = new schema.Entity(
        'charters',
        {
            children: [child],
        },
        {
            idAttribute: 'guid',
        }
    );

    const normalizedData = normalize(arrayChildren, [charter]);

    return normalizedData;
};

