import React, { FC, useContext } from 'react';
import cls from './PositionIItem.module.css';
import classNames from 'classnames';
import { Position } from '../../../store/reducers/positionSlice/positionSlice.types';
import { Context } from '../../Gant/Gant';
import { usePlanFact } from '../../../hooks/usePlanFact';
import { ConnectionArrow } from '../ConnectionArrow/ConnectionArrow';

interface PlanFactPositionItemProps {
    position: Position;
    coordinates: any;
    elWidth: any;
    positionX: any;
    chainPositionMove: any;
}
export const PlanFactPositionItem: FC<PlanFactPositionItemProps> = ({
    position,
    coordinates,
    positionX,
    chainPositionMove,
    elWidth,
}) => {
    const { activeTab, isPlanFact } = useContext(Context);

    const ellipsis = 'ellipsis';
    const clip = 'clip';

    const { planWidth, factWidth, factLeft } = usePlanFact(position!, 'position', activeTab);
    return (
        <div className={classNames(cls.wrapper)}>
            {!!position.connections.length &&
                coordinates.map((coordinate: any, index: any) => (
                    <ConnectionArrow
                        type={coordinate.type}
                        key={coordinate.connection.from + coordinate.connection.to + index}
                        left={elWidth}
                        positionX={positionX + chainPositionMove.positionX}
                        height={coordinate.height}
                        width={coordinate.width}
                        positionWidth={coordinate.positionWidth}
                        connection={coordinate.connection}
                    />
                ))}
            <div className={cls.planElementWrapper}>
                <div style={{ width: planWidth }} className={classNames(cls.planElement)}></div>
            </div>
            <div
                data-position-guid={position.guid}
                style={{ left: factLeft }}
                className={classNames(cls.StageItem, { [cls.planFact]: isPlanFact })}
            >
                <div
                    title={position?.name}
                    style={
                        {
                            width: factWidth,
                            cursor: 'auto',
                            '--width': position.done_percent,
                            '--name': `"${position.name}"`,
                            '--ellipsis': `${position.done_percent > 96 ? ellipsis : clip}`,
                        } as React.CSSProperties
                    }
                    className={classNames(cls.MainElement, [cls[position.type]], {
                        [cls.backgroundFact]: isPlanFact,
                        [cls.canceled]: isPlanFact && position.is_canceled,
                    })}
                >
                    <span className={cls.text}>{position?.name}</span>
                </div>
            </div>
        </div>
    );
};
