import { createSlice, createEntityAdapter } from '@reduxjs/toolkit';
import { RootState } from '../../store';
import { Charter } from './charterSlice.types';

const charterAdapter = createEntityAdapter<Charter>({
    selectId: (payment) => payment.guid,
});

const initialState = charterAdapter.getInitialState();

const charterSlice = createSlice({
    name: 'charter',
    initialState,
    reducers: {
        addStages: charterAdapter.setMany,
        updateOne: charterAdapter.updateOne,
        updateMany: charterAdapter.updateMany,
    },
});

export const charterSelector = charterAdapter.getSelectors((state: RootState) => state.charter);
export const { actions: charterActions } = charterSlice;
export default charterSlice.reducer;
