import React, { FC, memo, useContext } from 'react';
import cls from './StageItem.module.css';
import classNames from 'classnames';
import { Stage } from '../../../store/reducers/stageSlice/stageSlice.types';
import { Context } from '../../Gant/Gant';
import { ItemResizer } from '../ItemResizer/ItemResizer';
import { ItemMover } from '../ItemMover/ItemMover';
import { useResizeStage } from '../../../hooks/useResizeStage';
import { useMoveStage } from '../../../hooks/useMoveStage';
import { PlanFactStageItem } from './PlanFactStageItem';

interface StageItemProps {
    stage?: Stage;
    isPlanFact: boolean;
    id: string;
}

export const StageItem: FC<StageItemProps> = memo(({ stage, isPlanFact, id }) => {
    const { activeTab } = useContext(Context);

    const { positionX, handleMouseMove, handleMouseUp } = useMoveStage(stage!, activeTab);

    const { elWidth, changeWidth, onItemChange } = useResizeStage(stage!, activeTab);

    if (!stage) {
        return null;
    }

    const isSmall = elWidth < 97;

    return (
        <>
            {isPlanFact ? (
                <PlanFactStageItem stage={stage} />
            ) : (
                <>
                    <div className={classNames(cls.wrapper)}>
                        <div
                            id={`${stage.id}`}
                            style={{ left: positionX }}
                            className={classNames(cls.StageItem, { [cls.planFact]: isPlanFact })}
                        >
                            <svg
                                width="12"
                                height="28"
                                viewBox="0 0 12 28"
                                fill="none"
                                xmlns="http://www.w3.org/2000/svg"
                            >
                                <g clipPath="url(#clip0_1029_15465)">
                                    <path
                                        d="M0 4C0 1.79086 1.79086 0 4 0H8C10.2091 0 12 1.79086 12 4V24C12 26.2091 10.2091 28 8 28H4C1.79086 28 0 26.2091 0 24V4Z"
                                        fill="#E6E6E6"
                                    />
                                    <path
                                        d="M12 3.99915H10C9.928 2.53129 9.55264 1.98345 8 1.99957H4C2.49529 2.12342 2.14807 2.62194 2 3.99915V23.9949C2.00803 25.4074 2.48949 25.8492 4 25.9944H8C9.49929 26.0867 9.92232 25.3067 10 23.9949L12 23.9979C12 26.2961 10.3 27.9961 8 27.9971L4 27.994C1.7 27.9961 0 26.2961 0 23.9949V3.99915C0 1.69609 1.7 -0.00390625 4 0H8C10.3 -0.000566333 12 1.69943 12 3.99915Z"
                                        fill="#1094F7"
                                    />
                                </g>
                                <defs>
                                    <clipPath id="clip0_1029_15465">
                                        <rect width="12" height="28" fill="white" />
                                    </clipPath>
                                </defs>
                            </svg>

                            <ItemMover onMove={handleMouseMove} onMouseUp={handleMouseUp}>
                                <div
                                    title={stage.name}
                                    style={{ width: elWidth }}
                                    className={classNames(cls.MainElement, {
                                        [cls.backgroundFact]: isPlanFact,
                                        [cls.small]: isSmall,
                                    })}
                                >
                                    <span className={cls.text}>{stage?.name}</span>
                                </div>
                            </ItemMover>

                            <div className={classNames(cls.resizerStage)}>
                                <ItemResizer
                                    onResize={changeWidth}
                                    elWidth={elWidth}
                                    onStageChange={onItemChange}
                                    classes={cls.resizer}
                                />
                                <svg
                                    style={{ marginLeft: '-1px', cursor: 'ew-resize' }}
                                    width="12"
                                    height="28"
                                    viewBox="0 0 12 28"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                >
                                    <g clipPath="url(#clip0_1036_29472)">
                                        <path
                                            d="M0 4C0 1.79086 1.79086 0 4 0H8C10.2091 0 12 1.79086 12 4V24C12 26.2091 10.2091 28 8 28H4C1.79086 28 0 26.2091 0 24V4Z"
                                            fill="#E6E6E6"
                                        />
                                        <path
                                            d="M9.53674e-07 3.99915H2C2.072 2.53129 2.44736 1.98345 4 1.99958H8C9.50471 2.12343 9.85193 2.62195 10 3.99915V23.9949C9.99197 25.4074 9.51051 25.8492 8 25.9944H4C2.50071 26.0867 2.07768 25.3067 2 23.9949L0 23.9979C0 26.2961 1.7 27.9961 4 27.9971L8 27.994C10.3 27.9961 12 26.2961 12 23.9949V3.99915C12 1.6961 10.3 -0.00389953 8 6.71804e-06H4C1.7 -0.000559614 0 1.69944 9.53674e-07 3.99915Z"
                                            fill="#1094F7"
                                        />
                                    </g>
                                    <defs>
                                        <clipPath id="clip0_1036_29472">
                                            <rect width="12" height="28" fill="white" />
                                        </clipPath>
                                    </defs>
                                </svg>
                            </div>
                        </div>
                    </div>
                </>
            )}
        </>
    );
});
