import React, { FC, memo, useContext } from 'react';
import cls from './MonthModeHeader.module.css';
import classNames from 'classnames';
import { Context } from '../../../../Gant';
import moment from 'moment/moment';

interface DaysModeHeaderProps {}
export const MonthModeHeader: FC<DaysModeHeaderProps> = memo(() => {
    const { months } = useContext(Context);

    const nowDate = moment().format('DD.MM.YYYY');

    return (
        <div className={classNames(cls.DaysModeHeader)}>
            {months.map((year) => (
                <div className={classNames(cls.yearsWrapper)} key={year.title}>
                    <div className={classNames(cls.year)}>{year.title}</div>
                    <div className={classNames(cls.months)}>
                        {year.month.map((item) => (
                            <div
                                key={item.title + year.title}
                                className={classNames(cls.month)}
                                style={{ width: item.dayInMonth * 7 }}
                            >
                                <span className={cls.title}>{item.title}</span>
                                {item.days.map((day) => (
                                    <div
                                        key={day.date}
                                        data-date-now={day.date === nowDate}
                                        style={{ width: 7 }}
                                        className={classNames({
                                            [cls.nowDate]: day.date === nowDate,
                                        })}
                                    />
                                ))}
                            </div>
                        ))}
                    </div>
                </div>
            ))}
        </div>
    );
});
