import React, { FC, useEffect, useState } from 'react';
import Modal from '../../../../UI/Modal/Modal';
import { ModalHeader } from '../../../../UI/Modal/ModalHeader/ModalHeader';
import { ModalBody } from '../../../../UI/Modal/ModalBody/ModalBody';
import cls from './SettingsModal.module.css';
import { Switcher } from '../../../../UI/Switcher/Switcher';
import { ModalFooter } from '../../../../UI/Modal/ModalFooter/ModalFooter';
import { Button, ThemeButton } from '../../../../UI/Button/Button';
import { useSelector } from 'react-redux';
import { getGantData } from '../../../../../store/reducers/gantSlice/selectors/getGantData';
import { DayInput } from '../../../../UI/DayInput/DayInput';
import { useAppDispatch } from '../../../../../hooks/redux';
import { gantActions } from '../../../../../store/reducers/gantSlice/gantSlice';

interface SettingsModalProps {
    isOpen: boolean;
    onShowChange: (value: boolean) => void;
}
export const SettingsModal: FC<SettingsModalProps> = ({ isOpen, onShowChange }) => {
    const { settings } = useSelector(getGantData);
    const dispatch = useAppDispatch();
    const [modalForm, setModalForm] = useState({
        days: `${settings.daysMHD}`,
        isShowMHD: settings.isShowMHD,
    });
    const onHideModal = () => {
        onShowChange(false);
    };

    useEffect(() => {
        setModalForm({
            days: `${settings.daysMHD}`,
            isShowMHD: settings.isShowMHD,
        });
    }, [settings.isShowMHD]);

    const onChangeInput = (value: string) => {
        setModalForm({ ...modalForm, days: value });
    };

    const onSave = () => {
        dispatch(
            gantActions.changeSettings({ daysMHD: modalForm.days, isShowMHD: modalForm.isShowMHD })
        );
        onHideModal();
    };

    const onChangeSwitcher = () => {
        setModalForm({ ...modalForm, isShowMHD: !modalForm.isShowMHD });
    };

    return (
        <Modal isOpen={isOpen} onHideModal={onHideModal}>
            <ModalHeader>Настройки графика</ModalHeader>
            <ModalBody width={'492px'}>
                <div className={cls.body}>
                    <div className={cls.bodyBlock}>
                        <div className={cls.title}>
                            Отображать в графике материалы, механизмы и доставку
                        </div>
                        <div className={cls.setting}>
                            <Switcher
                                title={''}
                                isActive={modalForm.isShowMHD}
                                onClick={onChangeSwitcher}
                            />
                        </div>
                    </div>
                    <div className={cls.bodyBlock}>
                        <div className={cls.title}>
                            Автопланирование материалов, механизмов и доставки
                        </div>

                        <div className={cls.setting}>
                            <div className={cls.settingContent}>
                                <span>за</span>
                                <DayInput
                                    onChange={onChangeInput}
                                    value={`${modalForm.days}`}
                                    classes={cls.inputModal}
                                />
                                <span>дней до начала работ</span>
                            </div>
                        </div>
                    </div>
                </div>
            </ModalBody>
            <ModalFooter>
                <div className={cls.footer}>
                    <Button onClick={onHideModal} theme={ThemeButton.SECONDARY}>
                        Отмена
                    </Button>
                    <Button onClick={onSave}>Сохранить</Button>
                </div>
            </ModalFooter>
        </Modal>
    );
};
