import { useDispatch, useSelector } from 'react-redux';
import { stageSelector } from '../store/reducers/stageSlice/stageSlice';
import { charterSelector } from '../store/reducers/charterSlice/charterSlice';
import { positionSelector } from '../store/reducers/positionSlice/positionSlice';
import {
    Connections,
    Planned,
    Position,
} from '../store/reducers/positionSlice/positionSlice.types';
import { useUpdateAll } from './useUpdateAll';
import moment from 'moment';
import { gantActions } from '../store/reducers/gantSlice/gantSlice';
import { getGantData } from '../store/reducers/gantSlice/selectors/getGantData';

export const useAutoPlanningMhd = () => {
    const { updateAll, getStageChildren } = useUpdateAll();

    const dispatch = useDispatch();
    const { settings } = useSelector(getGantData);
    const stages = useSelector(stageSelector.selectAll);
    const chartersEntities = useSelector(charterSelector.selectEntities);
    const positions = useSelector(positionSelector.selectAll);
    const positionsEntities = useSelector(positionSelector.selectEntities);

    const isNotMHD = !positions.some(
        (position) => position.type !== 'labor' && !position.planned.start_date
    );

    const addConnect = (
        type: number,
        positionTo: Position,
        positionFrom: Position,
        autOPlanningForm: { days: string }
    ) => {
        const connection: Connections = {
            to: positionTo.guid,
            from: positionFrom.guid,
            type,
        };

        const plannedMHD: Planned = {
            days_count: autOPlanningForm.days,
            end_date: moment(positionFrom.planned.start_date).add(-1, 'days').format(),
            start_date: moment(positionFrom.planned.start_date)
                .add(-+autOPlanningForm.days, 'days')
                .format(),
        };

        const updatedPositionTo: Position = {
            ...positionTo,
            planned: plannedMHD,
            connections: [...positionTo.connections, connection],
        };
        const updatedPositionFrom: Position = {
            ...positionFrom,
            connections: [...positionFrom.connections, connection],
        };

        return { updatedPositionTo, updatedPositionFrom };
    };

    const autoPlanningMhd = (autOPlanningForm: { days: string }) => {
        const resultEntities = { ...positionsEntities };
        dispatch(gantActions.changeSettings({ ...settings, isShowMHD: true }));
        stages.forEach((stage) => {
            const { allPositions } = getStageChildren(positionsEntities, stage);

            const firstStagePosition = [...allPositions]
                .filter((position) => position.type === 'labor')
                .filter((position) => position.planned.start_date)
                .sort(
                    (positionA, positionB) =>
                        moment(positionA.planned.end_date!).valueOf() -
                        moment(positionB.planned.end_date!).valueOf()
                )
                .at(0)!;

            const stagePositions = stage.positions
                .filter((stageChildGuid) => !chartersEntities[stageChildGuid])
                .map((guid) => resultEntities[guid]);

            stagePositions.forEach((position) => {
                if (position?.type !== 'labor') {
                    const { updatedPositionTo, updatedPositionFrom } = addConnect(
                        1,
                        resultEntities[position!.guid]!,
                        resultEntities[firstStagePosition.guid]!,
                        autOPlanningForm
                    );
                    resultEntities[updatedPositionTo.guid] = updatedPositionTo;
                    resultEntities[updatedPositionFrom.guid] = updatedPositionFrom;
                }
            });

            const stageCharters = stage.positions
                .filter((stageChildGuid) => !!chartersEntities[stageChildGuid])
                .map((guid) => chartersEntities[guid]);

            stageCharters.forEach((charter) => {
                const charterPosition = charter!.children.map((guid) => resultEntities[guid]!);
                const firstCharterLabor = charterPosition
                    .filter((position) => position.type === 'labor')
                    .filter((position) => position.planned.start_date)
                    .sort(
                        (positionA, positionB) =>
                            moment(positionA.planned.end_date!).valueOf() -
                            moment(positionB.planned.end_date!).valueOf()
                    )
                    .at(0);

                const positionProm = firstCharterLabor ? firstCharterLabor : firstStagePosition;

                charterPosition.forEach((position) => {
                    if (position?.type !== 'labor') {
                        const { updatedPositionTo, updatedPositionFrom } = addConnect(
                            1,
                            resultEntities[position!.guid]!,
                            resultEntities[positionProm.guid]!,
                            autOPlanningForm
                        );
                        resultEntities[updatedPositionTo.guid] = updatedPositionTo;
                        resultEntities[updatedPositionFrom.guid] = updatedPositionFrom;
                    }
                });
            });
        });

        updateAll(resultEntities);
    };
    return { autoPlanningMhd, isNotMHD, stages };
};
