import React, { FC, useContext, useEffect, useState } from 'react';
import cls from './CharterItem.module.css';
import classNames from 'classnames';
import { useShowBlock } from '../../../../../hooks/useShowBlock';
import { ArrowBlock } from '../../../../UI/ArrowBlock/ArrowBlock';
import { PositionItem } from '../PositionItem/PositionItem';
import { Context } from '../../../Gant';
import { Charter } from '../../../../../store/reducers/charterSlice/charterSlice.types';
import { useSelector } from 'react-redux';
import { positionSelector } from '../../../../../store/reducers/positionSlice/positionSlice';
import { DayInput } from '../../../../UI/DayInput/DayInput';
import { DateInput } from '../../../../UI/DateInput/DateInput';
import moment from 'moment/moment';
import { useEditCharter } from '../../../../../hooks/useEditCharter';
import { getGantData } from '../../../../../store/reducers/gantSlice/selectors/getGantData';
import s from '../../../../UI/DateInput/DataInput.module.css';
import { Popup } from '../../../../UI/Popup/Popup';

interface SectionItemProps {
    charter?: Charter;
}
export const CharterItem: FC<SectionItemProps> = ({ charter }) => {
    const { isPlanFact, setIsOpenCharters, isOpenCharters } = useContext(Context);

    const { resetCharter, crateCharter, increaseCharter, decreaseCharter, onDataPickerCharter } =
        useEditCharter(charter!);

    const positions = useSelector(positionSelector.selectEntities);
    const { settings } = useSelector(getGantData);

    const [isShowPopup, setState] = useState(false);

    const onChangePopup = (state: boolean, e: any) => {
        e.stopPropagation();
        if (isPlanFact) {
            setState(state);
        }
    };

    const [charterForm, setCharterForm] = useState<{ days: string; date: string | null }>({
        date: charter!.planned.start_date ? moment(charter!.planned.start_date).format() : null,
        days: charter!.planned.days_count ? charter!.planned.days_count : '',
    });

    const pickerEndDate = charterForm.date
        ? moment(charterForm.date!)
              .add(+charterForm.days - 1, 'days')
              .format('DD.MM.YYYY')
        : moment(Date.now()).format('DD.MM.YYYY');

    const isLabor = charter?.children.some((guid) => positions[guid]!.type === 'labor');
    const isShowPosition = isLabor || settings.isShowMHD;

    useEffect(() => {
        setCharterForm({
            date: charter!.planned.start_date ? moment(charter!.planned.start_date).format() : null,
            days: charter!.planned.days_count ? charter!.planned.days_count : '',
        });
    }, [charter]);

    const { isShow, onShow } = useShowBlock(`${charter?.guid}`, isOpenCharters, setIsOpenCharters);

    const isNotNeedInitCharter = charter!.children.some(
        (positionGuid) => positions[positionGuid]!.planned.start_date !== null
    );

    const onDateChange = (value: string) => {
        setCharterForm((prevState) => ({ ...charterForm, days: value }));
    };

    const onSubmit = () => {
        if (charterForm.days === '0' || !charterForm.days) {
            resetCharter(charter!);
            return;
        }

        if (isNotNeedInitCharter) {
            const newEndDate = moment(charterForm.date!).add(+charterForm.days - 1, 'days');
            const oldEndDate = moment(charter!.planned.end_date);
            const isDecrease = newEndDate.isBefore(oldEndDate);
            const differenceInDays = Math.abs(oldEndDate.diff(newEndDate, 'days'));

            if (isDecrease) {
                decreaseCharter(charter!, differenceInDays);
            } else {
                increaseCharter(
                    charter!,
                    {
                        days: charterForm.days,
                        date: moment(charterForm.date!).format(),
                    },
                    differenceInDays
                );
            }
        } else {
            crateCharter(charter!, charterForm);
        }
    };

    const onPaymentDateChangeCancel = () => {
        resetCharter(charter!);
    };
    const onPaymentDateChange = ({ start, end }: { start: string; end: string }) => {
        const startDate = moment(start);
        const endDate = moment(end);
        const duration = moment.duration(endDate.diff(startDate));
        const days = `${Math.round(+duration.asDays())}`;

        // const oldEndDate = moment(charter!.planned.end_date);
        // const isDecrease = endDate.isBefore(oldEndDate);
        //
        // const diffNewStartOldStart = moment(start).diff(
        //     moment(charter?.planned.start_date!),
        //     'days'
        // );
        // const differenceInDays = Math.abs(oldEndDate.diff(endDate, 'days')) - diffNewStartOldStart;

        if (!isNotNeedInitCharter) {
            crateCharter(charter!, { days: days, date: moment(start).format() });
        } else {
            onDataPickerCharter(charter!, { start, end, days });
        }
    };

    let isPlanFactShow = true;

    if (isPlanFact) {
        isPlanFactShow = isPlanFact && !!charter!.planned.start_date;
    }

    if (!isPlanFactShow) {
        return null;
    }

    if (!charter || !isShowPosition) return null;

    return (
        <div className={classNames(cls.wrapper)} onClick={onShow}>
            <Popup
                isShow={isShowPopup}
                text={'Смета находится в работе, поэтому редактирование данных невозможно'}
                onHide={(e: any) => onChangePopup(false, e)}
            />
            <div className={classNames(cls.charterItem, { [cls.factWrapper]: isPlanFact })}>
                {/*<div className={cls.charterItemHover} />*/}
                <div className={classNames(cls.numberBlock)}>{charter?.number}</div>
                <ArrowBlock isShow={isShow} />
                <div className={classNames(cls.title)}>{charter?.name}</div>
                <div className={classNames(cls.capacity)} />
                <div className={classNames(cls.dateBlock)} onClick={(e) => onChangePopup(true, e)}>
                    {isPlanFact ? (
                        <>
                            {!!charter.planned.start_date ? (
                                <span className={classNames(cls.factDate)}>
                                    {moment(charter.planned.start_date).format('DD.MM.YYYY')}
                                </span>
                            ) : (
                                <div className={classNames(s.svgWrapper)}>
                                    <svg
                                        width="20"
                                        height="20"
                                        viewBox="0 0 20 20"
                                        fill="none"
                                        xmlns="http://www.w3.org/2000/svg"
                                    >
                                        <path d="M16.25 5H3.25V17H16.25V5Z" stroke="#757575" />
                                        <path d="M7.25 3H5.25V7H7.25V3Z" stroke="#757575" />
                                        <path
                                            d="M16.5 8.75H3V9.25H16.5V8.75Z"
                                            stroke="#757575"
                                            strokeWidth="0.5"
                                        />
                                        <path d="M14.25 3H12.25V7H14.25V3Z" stroke="#757575" />
                                    </svg>
                                </div>
                            )}
                        </>
                    ) : (
                        <>
                            <DateInput
                                key={Math.random()}
                                isRequired={true}
                                date={charterForm.date}
                                parameterName="datePlan"
                                newCallbackNotSingle={onPaymentDateChange}
                                newCallbackNotSingleCancel={onPaymentDateChangeCancel}
                                classes={[cls.charterInput, cls.input]}
                                options={{
                                    startDate: charterForm.date
                                        ? moment(charterForm.date).format('DD.MM.YYYY')
                                        : moment(Date.now()).format('DD.MM.YYYY'),
                                    endDate: pickerEndDate,
                                    autoUpdateInput: true,
                                    singleDatePicker: false,
                                    showDropdowns: true,
                                    autoApply: false,
                                }}
                            />
                        </>
                    )}

                    {isPlanFact && charter?.planned.start_date !== charter?.actual?.start_date && (
                        <span className={classNames(cls.fact)}>
                            {moment(charter?.actual?.start_date).format('DD.MM.YYYY')}
                        </span>
                    )}
                </div>
                <div className={classNames(cls.daysBlock)} onClick={(e) => onChangePopup(true, e)}>
                    <>
                        {isPlanFact ? (
                            <>
                                {charter.planned.days_count ? (
                                    <span>{charter.planned.days_count}</span>
                                ) : (
                                    <span style={{ color: '#ADB0B5' }}>0</span>
                                )}
                            </>
                        ) : (
                            <DayInput
                                classes={cls.charterInput}
                                value={charterForm.days}
                                onChange={onDateChange}
                                onSubmit={onSubmit}
                            />
                        )}

                        {isPlanFact &&
                            charter?.planned.days_count !== charter?.actual?.days_count && (
                                <span className={classNames(cls.fact)}>
                                    {charter?.actual?.days_count}
                                </span>
                            )}
                    </>
                </div>
            </div>
            {isShow && (
                <div
                    className={classNames(cls.positionBlock)}
                    onClick={(event) => event.stopPropagation()}
                >
                    {charter?.children.map((positionGuid) => (
                        <PositionItem
                            key={positionGuid}
                            position={positions[positionGuid]}
                            parentNumber={charter?.number}
                        />
                    ))}
                </div>
            )}
        </div>
    );
};
