import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { ConnectionsState, First, Second } from './connectionSlice.types';

const initialState: ConnectionsState = {
    first: {
        guid: '',
        x: 0,
        y: 0,
        position: '',
        type: 0,
    },
    second: {
        positionWidth: 0,
    },
    isConnection: false,
};

const connectionSlice = createSlice({
    name: 'connection',
    initialState,
    reducers: {
        setFirst: (state, action: PayloadAction<First>) => {
            state.isConnection = true;
            state.first = action.payload;
        },
        setSecond: (state, action: PayloadAction<Second>) => {
            state.second = action.payload;
        },
        endConnection: (state) => {
            state.isConnection = false;
            state.first = {
                guid: '',
                x: 0,
                y: 0,
                position: '',
                type: 0,
            };
            state.second = {
                positionWidth: 0,
            };
        },
    },
});

export const { actions: connectionActions } = connectionSlice;
export default connectionSlice.reducer;
