import { Planned, Position } from '../store/reducers/positionSlice/positionSlice.types';
import { useAppDispatch } from './redux';
import { useSelector } from 'react-redux';
import { positionActions, positionSelector } from '../store/reducers/positionSlice/positionSlice';
import { getLag } from '../helpers/getLag';
import moment from 'moment';
import { useEffect } from 'react';
import { Dictionary } from '@reduxjs/toolkit';

export const useUpdateConnectionPositions = () => {
    const allPositions = useSelector(positionSelector.selectEntities);

    const updateConnectionPositions = (
        position: Position,
        allPosition = allPositions
    ): Dictionary<Position> => {
        const result = { ...allPosition };

        const isFromConnection = position.connections.some(
            (connection) => connection.from === position.guid
        );

        if (isFromConnection) {
            const connection = position.connections.find(
                (connection) => connection.from === position.guid
            );

            if (!result[connection!.to]) {
                return result;
            }

            const positionTo = result[connection!.to]!;
            const lag = getLag(positionTo.planned.start_date!, position.planned.end_date!);

            const startDate = moment(positionTo.planned.start_date).add(-lag, 'days').format();

            const endDate = moment(positionTo.planned.end_date).add(-lag, 'days').format();

            const planned: Planned = {
                ...positionTo.planned,
                start_date: startDate,
                end_date: endDate,
            };

            const updatePosition = { ...positionTo, planned };

            const updatedPositions = {
                ...result,
                [updatePosition.guid]: updatePosition,
            };

            return updateConnectionPositions(updatePosition, updatedPositions);
        } else {
            return result;
        }
    };

    return { updateConnectionPositions };
};
