import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { GantData } from './getGant.types';

const initialState: GantData = {
    laborCount: 0,
    settings: {
        daysMHD: '3',
        isShowMHD: false,
    },
    chainPositionMove: {
        positionX: 0,
        guids: [],
    },
};

const gantSlice = createSlice({
    name: 'gant',
    initialState,
    reducers: {
        setLaborCount: (state, action: PayloadAction<number>) => {
            state.laborCount = action.payload;
        },
        changeSettings: (state, action: PayloadAction<{ daysMHD: string; isShowMHD: boolean }>) => {
            state.settings = action.payload;
        },
        changeChainPositionMove: (state, action: PayloadAction<number>) => {
            state.chainPositionMove.positionX = action.payload;
        },
        changeChainPositionMoveGuids: (state, action: PayloadAction<string[]>) => {
            state.chainPositionMove.guids = action.payload;
        },
    },
});

export const { actions: gantActions } = gantSlice;
export default gantSlice.reducer;
