import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { TableWidth } from './tableWidthSlice.types';

const initialState: TableWidth = {
    width: 480,
};

const tableWidthSlice = createSlice({
    name: 'tableWidth',
    initialState,
    reducers: {
        changeWidth: (state, action: PayloadAction<number>) => {
            state.width = action.payload;
        },
    },
});

export const { actions: tableWidthActions } = tableWidthSlice;
export default tableWidthSlice.reducer;
