import { useEffect, useState } from 'react';
import { Charter } from '../store/reducers/charterSlice/charterSlice.types';
import { getCountDays } from '../helpers/getCountDays';
import moment from 'moment';
import { useEditCharter } from './useEditCharter';

export const useMoveCharter = (charter: Charter, activeTab: 'day' | 'week' | 'month') => {
    const [positionX, setPositionX] = useState(0);
    const [needMoveUpdate, setNeedMoveUpdate] = useState(false);

    const { moveCharter } = useEditCharter(charter);

    const handleMouseMove = (value: number) => {
        setPositionX((prevState) => prevState + value);
    };

    const handleMouseUp = () => {
        setNeedMoveUpdate(true);
    };

    useEffect(() => {
        if (needMoveUpdate) {
            const oldStartDate = charter!.planned.start_date ? charter!.planned.start_date : '';
            const start_date = getCountDays(positionX, oldStartDate, activeTab);

            const dateDiff = moment(start_date).diff(oldStartDate, 'days');

            if (dateDiff !== 0) {
                moveCharter(charter, dateDiff);
            }

            setPositionX(0);
            setNeedMoveUpdate(false);
        }
    }, [needMoveUpdate]);

    return {
        positionX,
        handleMouseMove,
        handleMouseUp,
    };
};
