import { useState } from 'react';
import { IContext } from './Gant';
import moment from 'moment/moment';
import { isWeekend } from '../../helpers/isWeekend';
import { getDays, getMonths, getWeeks } from '../../helpers/getDays';

export const useGant = () => {
    const initIsOpensStages: Record<string, boolean> = {};
    const initIsOpensCharters: Record<string, boolean> = {};
    const initIsOpensPositions: Record<string, boolean> = {};

    // useEffect(() => {
    //     stages.forEach((stage) => {
    //         initIsOpensStages[stage.guid] = false;
    //         stage.charters.forEach((charter) => {
    //             initIsOpensCharters[charter.guid] = false;
    //             charter.positions.forEach((position) => {
    //                 initIsOpensPositions[position.guid] = false;
    //             });
    //         });
    //     });
    //
    //     setIsOpenStages(initIsOpensStages);
    //     setIsOpenCharters(initIsOpensCharters);
    //     setIsOpenPositions(initIsOpensPositions);
    // }, []);

    const [isOpenStages, setIsOpenStages] = useState(initIsOpensStages);
    const [isOpenCharters, setIsOpenCharters] = useState(initIsOpensCharters);
    const [isOpenPositions, setIsOpenPositions] = useState(initIsOpensPositions);
    const [isShowPopup, setIsShowPopup] = useState(false);
    const [isPlanFact, setIsPlanFact] = useState<boolean>(false);
    const [activeTab, setActiveTab] = useState<'day' | 'week' | 'month'>('day');

    const currentMonth = moment().format('MM.YYYY');

    const days = getDays(currentMonth);

    const months = getMonths(currentMonth);

    const weeks = getWeeks(currentMonth);

    const contextValue: IContext = {
        isPlanFact,
        setIsPlanFact,
        activeTab,
        setActiveTab,
        isOpenStages,
        setIsOpenStages,
        isOpenCharters,
        setIsOpenCharters,
        isOpenPositions,
        setIsOpenPositions,
        weeks,
        days,
        isShowPopup,
        setIsShowPopup,
        months,
    };
    return { contextValue };
};
