import React, { FC, useEffect, useRef } from 'react';
import classNames from 'classnames';
import cls from './PositionCircle.module.css';
import { useAppDispatch } from '../../../hooks/redux';
import { connectionActions } from '../../../store/reducers/connectionSlice/connectionSlice';
import { Planned, Position } from '../../../store/reducers/positionSlice/positionSlice.types';
import { useSelector } from 'react-redux';
import { getConnectionsData } from '../../../store/reducers/connectionSlice/selectors/getConnectionsData';
import { First } from '../../../store/reducers/connectionSlice/connectionSlice.types';
import { positionSelector } from '../../../store/reducers/positionSlice/positionSlice';
import moment from 'moment';
import { Dictionary } from '@reduxjs/toolkit';
import { getUpdatePositions } from '../../../helpers/getCountDays';
import { useUpdateAll } from '../../../hooks/useUpdateAll';
import { stageSelector } from '../../../store/reducers/stageSlice/stageSlice';
import { charterSelector } from '../../../store/reducers/charterSlice/charterSlice';

interface PositionCircleProps {
    classes?: string[];
    position: Position;
    positionCircle: string;
    widthPosition: number;
}

export const PositionCircle: FC<PositionCircleProps> = ({
    classes,
    position,
    positionCircle,
    widthPosition,
}) => {
    const ref = useRef<HTMLDivElement>(null);
    const dispatch = useAppDispatch();
    const allStage = useSelector(stageSelector.selectAll);
    const allCharters = useSelector(charterSelector.selectAll);
    const parentCharter = allCharters.find((charter) => charter.children.includes(position.guid));
    const parentStage = allStage.find((stage) => {
        const childGuid = parentCharter ? parentCharter.guid : position.guid;
        return stage.positions.includes(childGuid);
    });
    const { updateAll } = useUpdateAll(parentStage!);

    const allPositions = useSelector(positionSelector.selectEntities);

    const connection = useSelector(getConnectionsData);

    const getChainConnection = (
        position: Position,
        allPosition: Dictionary<Position>
    ): Position[] => {
        let result: Position[] = [];

        const [nextPosition] = position.connections.filter(
            (connect) => connect.type === 0 && connect.to !== position.guid
        );

        if (!nextPosition) {
            return result;
        }

        result = [...result, allPosition[nextPosition.to]!];
        const resultRec = getChainConnection(allPosition[nextPosition.to]!, allPosition);
        result = [...result, ...resultRec];
        return result;
    };

    const addConnection = (connectionData: First) => {
        const resultEntities: Dictionary<Position> = {};
        const currentPosition: Position = allPositions[connectionData.guid]!;

        const connectionGuids = currentPosition.connections.map(({ from }) => from);

        if (positionCircle === 'left' && connection.first.type === 1) {
            return;
        }

        if (positionCircle === 'right' && connection.first.type === 0) {
            return;
        }

        if (connectionGuids.includes(position.guid)) {
            return;
        }

        const newConnection = {
            from: currentPosition.guid,
            to: position.guid,
            type: connectionData.type,
        };

        const updatedConnections = [...currentPosition!.connections, newConnection];
        const updatedConnectionsTo = [...position!.connections, newConnection];

        const updatedPosition: Position = { ...currentPosition, connections: updatedConnections };
        const updatedPositionTo: Position = { ...position, connections: updatedConnectionsTo };

        const diffBeforePosition =
            moment(updatedPositionTo.planned.start_date).diff(
                moment(updatedPosition.planned.end_date),
                'days'
            ) - 1;

        let chainConnection: Position[] = getChainConnection(updatedPositionTo, allPositions);

        let startDateTo: string;

        let endDateTo: string;

        if (connectionData.type === 0) {
            startDateTo = moment(updatedPosition.planned.start_date)
                .add(updatedPosition.planned.days_count, 'days')
                .format();

            endDateTo = moment(startDateTo)
                .add(+updatedPositionTo.planned.days_count! - 1, 'days')
                .format();
        } else {
            startDateTo = moment(updatedPosition.planned.start_date)
                .add(-Number(updatedPositionTo.planned.days_count!), 'days')
                .format();

            endDateTo = moment(startDateTo)
                .add(+updatedPositionTo.planned.days_count! - 1, 'days')
                .format();
        }

        const plannedTo: Planned = {
            ...updatedPositionTo.planned,
            start_date: startDateTo!,
            end_date: endDateTo!,
        };

        resultEntities[updatedPosition.guid] = updatedPosition;
        resultEntities[updatedPositionTo.guid] = { ...updatedPositionTo, planned: plannedTo };

        if (diffBeforePosition < 0) {
            const diff = Math.abs(diffBeforePosition);

            const updateNextPosition = getUpdatePositions(chainConnection, diff);

            updateNextPosition.forEach((item) => {
                resultEntities[item.id] = item.changes;
            });
        }
        updateAll(resultEntities);
    };

    useEffect(() => {
        if (ref !== null) {
            const position = ref.current?.getBoundingClientRect();
        }
    }, [ref]);

    return (
        <div className={classNames([classes])}>
            <div
                className={classNames(cls.PositionCircle, {
                    [cls.active]:
                        positionCircle === connection.first.position &&
                        `${position.guid}` === connection.first.guid,
                    [cls.connecting]: connection.isConnection,
                })}
                ref={ref}
                onClick={(event) => {
                    if (!connection.isConnection) {
                        dispatch(
                            connectionActions.setFirst({
                                x: event.clientX,
                                y: event.clientY,
                                guid: `${position.guid}`,
                                position: positionCircle,
                                type: positionCircle === 'right' ? 0 : 1,
                            })
                        );
                    } else {
                        dispatch(connectionActions.setSecond({ positionWidth: widthPosition }));
                        addConnection(connection.first);
                        dispatch(connectionActions.endConnection());
                    }
                }}
            ></div>
        </div>
    );
};
