import React from 'react';
import { Stage } from '../store/reducers/stageSlice/stageSlice.types';
import { Charter } from '../store/reducers/charterSlice/charterSlice.types';
import { Position } from '../store/reducers/positionSlice/positionSlice.types';
import { getWidth, getWidthPosition } from '../helpers/getWidth';
import moment from 'moment/moment';

export const usePlanFact = (
    item: Stage | Charter | Position,
    type: 'stage' | 'charter' | 'position',
    activeTab: 'day' | 'week' | 'month'
) => {
    let planWidth = getWidth(Number(item.planned?.days_count), activeTab);
    let factWidth = getWidth(Number(item.actual?.days_count), activeTab);

    if (type === 'position') {
        planWidth = getWidthPosition(Number(item.planned?.days_count), activeTab);
        factWidth = getWidthPosition(Number(item.actual?.days_count), activeTab);
    }

    const startDateFact = item!.actual?.start_date ? item!.actual?.start_date : '';
    const startDatePlan = item!.planned.start_date ? item!.planned.start_date : '';

    const dateDiff = moment(startDateFact).diff(startDatePlan, 'days');

    let factLeft = dateDiff * 24;
    if (activeTab === 'week') {
        factLeft = dateDiff * 11;
    }

    if (activeTab === 'month') {
        factLeft = dateDiff * 7;
    }

    return { planWidth, factWidth, factLeft };
};
