import React, { FC, memo } from 'react';
import cls from './DaysModePosition.module.css';
import classNames from 'classnames';
import { PositionIItem } from '../../../../../../UI/PositionItem/PositionIItem';
import { Position } from '../../../../../../../store/reducers/positionSlice/positionSlice.types';
import moment from 'moment';
import { useSelector } from 'react-redux';
import { getGantData } from '../../../../../../../store/reducers/gantSlice/selectors/getGantData';

interface DayProps {
    month: {
        title: string;
        month: number;
        days: { day: number; isWeekend: boolean; date: string }[];
    };
    position: Position;
    isPlanFact: boolean;
    currentDay: number;
    currentMonths: number;
}
export const DayItemPosition: FC<DayProps> = memo(
    ({ month, position, isPlanFact, currentDay, currentMonths }) => {
        const nowDate = moment().format('DD.MM.YYYY');

        const { settings } = useSelector(getGantData);

        const isShowPosition = !settings.isShowMHD && position?.type !== 'labor';

        let isPlanFactShow = true;

        if (isPlanFact) {
            isPlanFactShow = isPlanFact && !!position?.planned.start_date;
        }

        if (!isPlanFactShow) {
            return null;
        }

        if (isShowPosition) {
            return null;
        }

        return (
            <>
                {month.days.map((day) => (
                    <div
                        key={day.day}
                        className={classNames(cls.day, {
                            [cls.factWrapper]: isPlanFact,
                            [cls.weekend]: day.isWeekend,
                            [cls.nowDate]: day.date === nowDate,
                        })}
                    >
                        {day.day === currentDay && currentMonths === month.month && (
                            <PositionIItem isPlanFact={isPlanFact} position={position} />
                        )}
                    </div>
                ))}
            </>
        );
    }
);
