import React, { FC, memo, useContext } from 'react';
import cls from './DaysModeCharter.module.css';
import classNames from 'classnames';
import { Context } from '../../../../../Gant';
import { DaysModePosition } from '../DaysModePosition/DaysModePosition';
import { Charter } from '../../../../../../../store/reducers/charterSlice/charterSlice.types';
import { useSelector } from 'react-redux';
import { positionSelector } from '../../../../../../../store/reducers/positionSlice/positionSlice';
import { DayItemCharter } from './DayItemCharter';
import moment from 'moment/moment';
import { getGantData } from '../../../../../../../store/reducers/gantSlice/selectors/getGantData';

interface DaysModeCharterProps {
    charter?: Charter;
}
export const DaysModeCharter: FC<DaysModeCharterProps> = memo(({ charter }) => {
    const { isPlanFact, isOpenCharters, days } = useContext(Context);
    const currentMonths = +moment(charter?.planned.start_date).locale('ru').format('MM');
    const currentDay = +moment(charter?.planned.start_date).locale('ru').format('DD');

    const positions = useSelector(positionSelector.selectEntities);
    const { settings } = useSelector(getGantData);
    const isLabor = charter?.children.some((guid) => positions[guid]!.type === 'labor');
    const isShowPosition = isLabor || settings.isShowMHD;
    if (!charter || !isShowPosition) {
        return null;
    }

    let isPlanFactShow = true;

    if (isPlanFact) {
        isPlanFactShow = isPlanFact && !!charter!.planned.start_date;
    }

    if (!isPlanFactShow) {
        return null;
    }

    return (
        <div className={classNames(cls.DaysModeCharter)}>
            <div className={classNames(cls.days)}>
                {days.map((month, index) => (
                    <DayItemCharter
                        month={month}
                        charter={charter}
                        isPlanFact={isPlanFact}
                        currentDay={currentDay}
                        currentMonths={currentMonths}
                        key={index}
                    />
                ))}
            </div>
            {isOpenCharters[charter.guid] && (
                <>
                    {charter.children.map((positionGuid) => (
                        <DaysModePosition
                            key={positionGuid + Date.now()}
                            position={positions[positionGuid]}
                        />
                    ))}
                </>
            )}
        </div>
    );
});
