import { useContext } from 'react';
import { Context } from '../../Gant';

export const useHeaderInterface = () => {
    const { isPlanFact, setIsPlanFact } = useContext(Context);
    const onSwitchChange = () => {
        setIsPlanFact((prevState) => !prevState);
    };
    return {
        isPlanFact,
        setIsPlanFact,
        onSwitchChange,
    };
};
