import React, { FC } from 'react';
import cls from './WeeksModePosition.module.css';
import classNames from 'classnames';
import { Position } from '../../../../../../../store/reducers/positionSlice/positionSlice.types';
import { useSelector } from 'react-redux';
import { getGantData } from '../../../../../../../store/reducers/gantSlice/selectors/getGantData';
import moment from 'moment/moment';
import { PositionIItem } from '../../../../../../UI/PositionItem/PositionIItem';

interface WeekItemPositionProps {
    week: { title: string; days: string[]; date: string };
    isPlanFact: boolean;
    position: Position;
}
export const WeekItemPosition: FC<WeekItemPositionProps> = ({ position, week, isPlanFact }) => {
    const { settings } = useSelector(getGantData);

    if (!position) return null;

    const positionDate = moment(position.planned.start_date).format('DD.MM.YYYY');

    const isShowPosition = !settings.isShowMHD && position?.type !== 'labor';
    const nowDate = moment().format('DD.MM.YYYY');

    const isRenderDays =
        week.date === moment(position.planned.start_date).format('MM.YYYY') ||
        week.date === moment().format('MM.YYYY');

    let isPlanFactShow = true;

    if (isPlanFact) {
        isPlanFactShow = isPlanFact && !!position?.planned.start_date;
    }

    if (!isPlanFactShow) {
        return null;
    }

    if (isShowPosition) {
        return null;
    }

    return (
        <div className={classNames(cls.week, { [cls.factWrapper]: isPlanFact })}>
            {isRenderDays &&
                week.days.map((day) => (
                    <div
                        key={day + Math.random().toString(36)}
                        className={classNames(cls.day, {
                            [cls.factWrapper]: isPlanFact,
                            [cls.nowDate]: day === nowDate,
                        })}
                        data-id={day}
                    >
                        {positionDate === day && (
                            <PositionIItem isPlanFact={isPlanFact} position={position} />
                        )}
                    </div>
                ))}
        </div>
    );
};
