import React from 'react';
import Modal from '../Modal/Modal';
import { ModalBody } from '../Modal/ModalBody/ModalBody';
import { ModalFooter } from '../Modal/ModalFooter/ModalFooter';
import { Button, ThemeButton } from '../Button/Button';

//@ts-ignore
export const Popup = ({ text, onHide, isShow }) => {
    return (
        <Modal isOpen={isShow} onHideModal={onHide}>
            <ModalBody width={'476px'}>{text}</ModalBody>
            <ModalFooter>
                <Button onClick={onHide} theme={ThemeButton.SECONDARY}>
                    Закрыть
                </Button>
            </ModalFooter>
        </Modal>
    );
};
