import { useSelector } from 'react-redux';
import { charterSelector } from '../store/reducers/charterSlice/charterSlice';
import { stageSelector } from '../store/reducers/stageSlice/stageSlice';
import { useUpdateAll } from './useUpdateAll';
import {
    Connections,
    Planned,
    Position,
} from '../store/reducers/positionSlice/positionSlice.types';
import moment from 'moment';

export const useEditPosition = (position: Position) => {
    const allCharters = useSelector(charterSelector.selectAll);
    const allStage = useSelector(stageSelector.selectAll);

    const parentCharter = allCharters.find((charter) => charter.children.includes(position.guid));

    const parentStage = allStage.find((stage) => {
        const childGuid = parentCharter ? parentCharter.guid : position.guid;

        return stage.positions.includes(childGuid);
    });

    const { updateAll } = useUpdateAll(parentStage!);

    const resetPosition = () => {
        const resetPlanned: Planned = {
            days_count: null,
            start_date: null,
            end_date: null,
        };

        const resetConnection: Connections[] = [];

        const updatedPosition: Position = {
            ...position,
            planned: resetPlanned,
            connections: resetConnection,
        };

        updateAll({ [updatedPosition.guid]: updatedPosition });
    };
    const createPosition = (positionForm: { days: string; date: string | null }) => {
        let currentDate = !!positionForm.date
            ? positionForm.date
            : moment().hour(0).minute(0).seconds(0).format();

        const planned: Planned = {
            days_count: positionForm.days,
            start_date: currentDate,
            end_date: moment(currentDate)
                .add(+positionForm.days - 1, 'days')
                .format(),
        };
        updateAll({ [position.guid]: { ...position, planned } });
    };
    const increasePosition = (
        differenceInDays: number,
        pickerData: { start: string; end: string; days: string } | null = null
    ) => {
        const updateCountDays = +position.planned.days_count! + differenceInDays;

        let planned: Planned = {
            ...position.planned,
            days_count: `${updateCountDays}`,
            end_date: moment(position.planned.start_date)
                .add(updateCountDays - 1, 'days')
                .format(),
        };
        if (pickerData) {
            planned = {
                days_count: pickerData.days,
                start_date: pickerData.start,
                end_date: pickerData.end,
            };
        }
        updateAll({ [position.guid]: { ...position, planned } });
    };
    const decreasePosition = (
        differenceInDays: number,
        pickerData: { start: string; end: string; days: string } | null = null
    ) => {
        const updateCountDays =
            +position.planned.days_count! - differenceInDays < 1
                ? 1
                : +position.planned.days_count! - differenceInDays;

        let planned: Planned = {
            ...position.planned,
            days_count: `${updateCountDays}`,
            end_date: moment(position.planned.start_date)
                .add(updateCountDays - 1, 'days')
                .format(),
        };
        if (pickerData) {
            planned = {
                days_count: pickerData.days,
                start_date: pickerData.start,
                end_date: pickerData.end,
            };
        }
        updateAll({ [position.guid]: { ...position, planned } });
    };

    return {
        resetPosition,
        createPosition,
        decreasePosition,
        increasePosition,
    };
};
