import { useContext } from 'react';
import { Context } from '../../Gant/Gant';

export const useModeTabs = () => {
    const { activeTab, setActiveTab } = useContext(Context);

    const onChangeActiveTab = (tab: 'day' | 'week' | 'month') => {
        setActiveTab(tab);
    };

    return { activeTab, onChangeActiveTab };
};
