import React, { FC, memo } from 'react';
import cls from './DaysModeCharter.module.css';
import classNames from 'classnames';
import { Charter } from '../../../../../../../store/reducers/charterSlice/charterSlice.types';
import { CharterItem } from '../../../../../../UI/CharterItem/CharterItem';
import moment from 'moment/moment';

interface DayProps {
    month: {
        title: string;
        month: number;
        days: { day: number; isWeekend: boolean; date: string }[];
    };
    charter: Charter;
    isPlanFact: boolean;
    currentDay: number;
    currentMonths: number;
}
export const DayItemCharter: FC<DayProps> = memo(
    ({ month, charter, isPlanFact, currentDay, currentMonths }) => {
        const nowDate = moment().format('DD.MM.YYYY');

        return (
            <>
                {month.days.map((day) => (
                    <div
                        key={day.day}
                        className={classNames(cls.day, {
                            [cls.factWrapper]: isPlanFact,
                            [cls.weekend]: day.isWeekend,
                            [cls.nowDate]: day.date === nowDate,
                        })}
                    >
                        {day.day === currentDay && currentMonths === month.month && (
                            <CharterItem isPlanFact={isPlanFact} charter={charter} />
                            // <StageItem isPlanFact={isPlanFact} stage={stage} id={`${stage.guid}`} />
                        )}
                    </div>
                ))}
            </>
        );
    }
);
