import { Dictionary } from '@reduxjs/toolkit';
import {
    Connections,
    Planned,
    Position,
} from '../store/reducers/positionSlice/positionSlice.types';
import moment from 'moment';
import { Charter } from '../store/reducers/charterSlice/charterSlice.types';

export const createWaterfall = (
    itemPositions: Position[],
    currentDate: string,
    positionsEntities: Dictionary<Position>,
    settings: { isShowMHD: boolean; daysMHD: string },
    lastLaborPositionGuid: string,
    plannedMhd: Planned,
    remainder: number,
    long: number,
    charters: Charter[] | [] = []
): Dictionary<Position> => {
    // const currentPositions = [...itemPositions];
    const labors = itemPositions.filter((position) => position.type === 'labor');
    const MHDs = itemPositions.filter((position) => position.type !== 'labor');
    const result: Dictionary<Position> = { ...positionsEntities };

    const firstPositionGuid = itemPositions[0].guid;

    const resultMHDPositions: Position[] = [];

    for (let i = 0; i < labors.length; i++) {
        if (i !== labors.length - 1) {
            const connectionsFrom: Connections[] = [
                ...labors[i].connections,
                { from: labors[i].guid, to: labors[i + 1].guid, type: 0 },
            ];

            const connectionsTo: Connections[] = [
                ...labors[i + 1].connections,
                { from: labors[i].guid, to: labors[i + 1].guid, type: 0 },
            ];

            const fromPosition: Position = {
                ...labors[i],
                connections: connectionsFrom,
            };
            const toPosition: Position = {
                ...labors[i + 1],
                connections: connectionsTo,
            };
            labors[i] = fromPosition;
            labors[i + 1] = toPosition;
        }
    }

    labors.forEach((position) => {
        result[position.guid] = position;
    });

    // resultMHDPositions.forEach((position) => {
    //     if (settings.isShowMHD) {
    //         result[position.guid] = position;
    //     }
    // });

    itemPositions.forEach(({ guid }) => {
        if (positionsEntities[guid]!.type === 'labor') {
            const isLastPosition = lastLaborPositionGuid === guid;

            const countDays = isLastPosition ? remainder + long : long;
            const planedPosition: Planned = {
                days_count: `${countDays}`,
                start_date: currentDate,
                end_date: moment(currentDate)
                    .add(countDays - 1, 'days')
                    .format(),
            };
            currentDate = moment(currentDate).add(long, 'days').format();

            const updatedPosition: Position = {
                ...result[guid]!,
                planned: planedPosition,
            };

            result[guid] = updatedPosition;
        } else {
            const updatePosition: Position = {
                ...result[guid]!,
                planned: plannedMhd,
            };

            if (settings.isShowMHD) result[guid] = updatePosition;
        }
    });

    if (settings.isShowMHD) {
        charters.forEach((charter) => {
            const charterPositions = charter.children.map((guid) => result[guid]);
            const charterMHDPositions = charterPositions.filter(
                (position) => position?.type !== 'labor'
            );
            const charterLaborPositions = charterPositions
                .filter((position) => position?.type === 'labor')
                .sort(
                    (positionA, positionB) =>
                        moment(positionA?.planned.start_date!).valueOf() -
                        moment(positionB?.planned.start_date!).valueOf()
                );
            if (charterLaborPositions.length) {
                charterMHDPositions.forEach((position) => {
                    if (position) {
                        const addDaysValue =
                            +settings.daysMHD > charterLaborPositions.length
                                ? charterLaborPositions.length
                                : +plannedMhd.days_count!;

                        const planned: Planned = {
                            days_count: `${addDaysValue}`,
                            start_date: moment(
                                charterLaborPositions.at(0)!.planned.start_date
                            ).format(),
                            end_date: moment(charterLaborPositions.at(0)!.planned.start_date)
                                .add(addDaysValue - 1)
                                .format(),
                        };

                        result[position.guid] = { ...result[position.guid]!, planned };
                    }
                });
            }
        });
    }

    return result;
};
