import { Stage } from '../store/reducers/stageSlice/stageSlice.types';
import { useSelector } from 'react-redux';
import { positionActions, positionSelector } from '../store/reducers/positionSlice/positionSlice';
import { charterActions, charterSelector } from '../store/reducers/charterSlice/charterSlice';
import { useAppDispatch } from './redux';
import {
    Connections,
    Planned,
    Position,
} from '../store/reducers/positionSlice/positionSlice.types';
import { Charter } from '../store/reducers/charterSlice/charterSlice.types';
import { actions } from '../store/reducers/stageSlice/stageSlice';
import { getGantData } from '../store/reducers/gantSlice/selectors/getGantData';
import moment from 'moment/moment';
import { useUpdateConnectionPositions } from './useUpdateConnectionPositions';
import { Dictionary } from '@reduxjs/toolkit';
import { getCountDaysFromDate, getUpdatePositions } from '../helpers/getCountDays';
import { useUpdateAll } from './useUpdateAll';
import { getLongPositions } from '../helpers/getLongPositions';
import { useContext } from 'react';
import { createWaterfall } from '../helpers/createWaterfall';
import { getChains } from '../helpers/getChains';
import { updateChain } from '../helpers/updateChain';
import { updatePositionWithoutConnect } from '../helpers/updatePositionWithoutConnect';
import { Context } from '../components/Gant/Gant';
import { getChainConnection } from '../helpers/getChainConnection';

export const useEditStage = (stage: Stage) => {
    const { setIsShowPopup } = useContext(Context);

    const dispatch = useAppDispatch();
    const allPositions = useSelector(positionSelector.selectEntities);
    const positionsEntities = useSelector(positionSelector.selectEntities);
    const allCharters = useSelector(charterSelector.selectEntities);

    const { updateConnectionPositions } = useUpdateConnectionPositions();

    const { settings } = useSelector(getGantData);

    const { updateAll, getStageChildren } = useUpdateAll(stage!);

    const { allPositions: stagePositions, charters } = getStageChildren(positionsEntities);
    const stageLabors = stagePositions.filter((position) => position.type === 'labor');
    const stageMHD = stagePositions.filter((position) => position.type !== 'labor');
    const lastLaborPosition = [...stagePositions]
        .filter((position) => position.type === 'labor')
        .sort(
            (positionA, positionB) =>
                moment(positionA.planned.end_date!).valueOf() -
                moment(positionB.planned.end_date!).valueOf()
        )
        .at(-1)!;

    const positionsWithConnection = stagePositions
        .filter((position) => !!position.connections.length)
        .sort(
            (positionA, positionB) =>
                moment(positionA.planned.end_date!).valueOf() -
                moment(positionB.planned.end_date!).valueOf()
        );

    const getChainConnection = (
        position: Position,
        allPosition: Dictionary<Position>
    ): Position[] => {
        let result: Position[] = [];

        const [nextPosition] = position.connections.filter(
            (connect) => connect.type === 0 && connect.to !== position.guid
        );

        if (!nextPosition) {
            return result;
        }

        result = [...result, allPosition[nextPosition.to]!];
        const resultRec = getChainConnection(allPosition[nextPosition.to]!, allPosition);
        result = [...result, ...resultRec];
        return result;
    };
    const onHidePopup = () => setIsShowPopup(false);

    const resetStage = (stage: Stage) => {
        const resetPlanned: Planned = {
            days_count: null,
            start_date: null,
            end_date: null,
        };

        const resetConnection: Connections[] = [];

        const updatedStage: Stage = { ...stage, planned: resetPlanned };
        const updatedCharters: { id: string; changes: Charter }[] = [];
        const updatedPositions: { id: string; changes: Position }[] = [];

        let resultPositionsEntities: Dictionary<Position> = { ...allPositions };

        stage.positions.forEach((stageChildGuid) => {
            if (!!allCharters[stageChildGuid]) {
                const updatedCharter: Charter = {
                    ...allCharters[stageChildGuid]!,
                    planned: resetPlanned,
                };

                updatedCharters.push({ id: stageChildGuid, changes: updatedCharter });

                allCharters[stageChildGuid]!.children.forEach((positionGuid) => {
                    const currentPosition = resultPositionsEntities[positionGuid]!;

                    if (currentPosition.connections.length) {
                        currentPosition.connections.forEach((connection) => {
                            if (connection.to === currentPosition.guid) {
                                const connectionsFrom = resultPositionsEntities[
                                    connection.from
                                ]!.connections.filter((connectionFrom) => {
                                    return connectionFrom.to !== connection.to;
                                });

                                resultPositionsEntities[connection.from] = {
                                    ...resultPositionsEntities[connection.from]!,
                                    connections: connectionsFrom,
                                };
                            } else {
                                const connectionsTo = resultPositionsEntities[
                                    connection.to
                                ]!.connections.filter(
                                    (connectionTo) => connectionTo.from !== connection.from
                                );

                                resultPositionsEntities[connection.to] = {
                                    ...resultPositionsEntities[connection.to]!,
                                    connections: connectionsTo,
                                };
                            }
                        });
                    }

                    const updatedPosition: Position = {
                        ...resultPositionsEntities[positionGuid]!,
                        planned: resetPlanned,
                        connections: resetConnection,
                    };
                    resultPositionsEntities[positionGuid] = updatedPosition;

                    // const updatedPosition: Position = {
                    //     ...allPositions[positionGuid]!,
                    //     planned: resetPlanned,
                    //     connections: resetConnection,
                    // };
                    // updatedPositions.push({ id: positionGuid, changes: updatedPosition });
                });
            } else {
                const currentPosition = resultPositionsEntities[stageChildGuid]!;

                if (currentPosition.connections.length) {
                    currentPosition.connections.forEach((connection) => {
                        if (connection.to === currentPosition.guid) {
                            const connectionsFrom = resultPositionsEntities[
                                connection.from
                            ]!.connections.filter((connectionFrom) => {
                                return connectionFrom.to !== connection.to;
                            });

                            resultPositionsEntities[connection.from] = {
                                ...resultPositionsEntities[connection.from]!,
                                connections: connectionsFrom,
                            };
                        } else {
                            const connectionsTo = resultPositionsEntities[
                                connection.to
                            ]!.connections.filter(
                                (connectionTo) => connectionTo.from !== connection.from
                            );

                            resultPositionsEntities[connection.to] = {
                                ...resultPositionsEntities[connection.to]!,
                                connections: connectionsTo,
                            };
                        }
                    });
                }

                const updatedPosition: Position = {
                    ...resultPositionsEntities[stageChildGuid]!,
                    planned: resetPlanned,
                    connections: resetConnection,
                };
                resultPositionsEntities[stageChildGuid] = updatedPosition;
                //     const updatedPosition: Position = {
                //         ...allPositions[stageChildGuid]!,
                //         planned: resetPlanned,
                //         connections: resetConnection,
                //     };
                //     updatedPositions.push({ id: stageChildGuid, changes: updatedPosition });
            }
        });

        dispatch(actions.updateOne({ id: stage.id, changes: updatedStage }));
        dispatch(charterActions.updateMany(updatedCharters));
        // dispatch(positionActions.updateMany(updatedPositions));
        updateAll(resultPositionsEntities);
    };

    const createStage = (stage: Stage, stageForm: { days: string; date: string | null }) => {
        let planned: Planned;

        // const { allPositions: stagePositions } = getStageChildren(positionsEntities);
        // const stageLabors = stagePositions.filter((position) => position.type === 'labor');
        // // const stageMHD = stagePositions.filter((position) => position.type !== 'labor');
        //
        // const lastLaborPosition = [...stagePositions]
        //     .filter((position) => position.type === 'labor')
        //     .sort(
        //         (positionA, positionB) =>
        //             moment(positionA.planned.end_date!).valueOf() -
        //             moment(positionB.planned.end_date!).valueOf()
        //     )
        //     .at(-1)!;

        if (!stageLabors.length) {
            let updatedPositionsEntities: Dictionary<Position> = {};

            const start_date = stageForm.date
                ? stageForm.date
                : moment().hour(0).minute(0).seconds(0).format();

            const end_date = moment(start_date)
                .add(+stageForm.days - 1, 'days')
                .format();

            stageMHD.forEach((position) => {
                updatedPositionsEntities[position.guid] = {
                    ...position,
                    planned: {
                        start_date,
                        end_date,
                        days_count: stageForm.days,
                    },
                };
            });

            updateAll(updatedPositionsEntities);
            return;
        }

        if (Number(stageForm.days) < stageLabors.length) {
            setIsShowPopup(true);
        }

        const laborCount = stagePositions.filter((position) => position.type === 'labor').length;

        const resultDays = Number(stageForm.days) < laborCount ? `${laborCount}` : stageForm.days;

        let currentDate = !!stageForm.date
            ? stageForm.date
            : moment().hour(0).minute(0).seconds(0).format();

        planned = {
            days_count: `${Number(resultDays)}`,
            start_date: currentDate,
            end_date: moment()
                .hour(0)
                .minute(0)
                .seconds(0)
                .add(Number(resultDays) - 1, 'days')
                .format(),
        };

        const updatedStage = { ...stage, planned };

        const { long, remainder } = getLongPositions(laborCount, +resultDays);

        const mhdDaysCount =
            Number(updatedStage.planned.days_count) < +settings.daysMHD
                ? Number(updatedStage.planned.days_count)
                : settings.daysMHD;

        const plannedMhd: Planned = {
            start_date: currentDate,
            end_date: moment(currentDate)
                .add(+mhdDaysCount - 1, 'days')
                .format(),
            days_count: `${mhdDaysCount}`,
        };

        const updatedEntities = createWaterfall(
            stagePositions,
            currentDate,
            positionsEntities,
            settings,
            lastLaborPosition.guid,
            plannedMhd,
            remainder,
            long,
            charters
        );

        updateAll(updatedEntities);
    };

    const increaseStage = (
        stage: Stage,
        charterForm: { days: string; date: string | null },
        difference: number,
        pickerData: { start: string; end: string; days: string } | null = null
    ) => {
        let updatedPositionsEntities = { ...allPositions };

        const endDate = moment(charterForm.date)
            .add(Number(charterForm.days) - 1, 'days')
            .format();

        const filteredPosition = [...stagePositions]
            .filter((position) => position.planned.end_date)
            .sort(
                (positionA, positionB) =>
                    moment(positionA.planned.end_date!).valueOf() -
                    moment(positionB.planned.end_date!).valueOf()
            );

        const lastPositionFiltered = filteredPosition.at(-1)!;

        const start = moment(lastPositionFiltered.planned.start_date!);
        const end = moment(endDate);
        const duration = moment.duration(end.diff(start));
        const days = `${Math.round(+duration.asDays()) + 1}`;

        const planned: Planned = {
            ...lastPositionFiltered.planned,
            days_count: days,
            end_date: endDate,
        };

        const updatedPosition: Position = { ...lastPositionFiltered, planned };

        const moveDays = +charterForm.days - +stage.planned.days_count!;

        const nextPositions = updatedPosition.connections
            .filter(
                (connection) => connection.type === 0 && updatedPosition.guid === connection.from
            )
            .map((connection) => allPositions[connection.to]!);

        const chainsConnections = nextPositions.map((position) => [
            position,
            ...getChainConnection(position, allPositions),
        ]);

        if (chainsConnections.length) {
            chainsConnections.forEach((chain) => {
                const nextPosition = chain.at(0)!;

                const initEndDate = allPositions[lastPositionFiltered.guid]!.planned.end_date;

                const nextPositionStartDate = nextPosition.planned.start_date!;

                const diffBeforePosition =
                    moment(nextPositionStartDate).diff(moment(initEndDate), 'days') - 1;

                if (diffBeforePosition - moveDays < 0) {
                    const diff = Math.abs(diffBeforePosition - moveDays);

                    const updatePosition = getUpdatePositions(chain, diff);

                    updatedPositionsEntities[updatedPosition!.guid] = updatedPosition;

                    updatePosition.forEach((item) => {
                        updatedPositionsEntities[item.id] = item.changes;
                    });
                }
            });
        }

        // const initEndDate = allPositions[updatedPosition.guid]!.planned.end_date!;
        //
        // let initNextPositionStartDate: string | null;
        // let diffBeforePosition: number = 0;
        //
        // let chainConnection: Position[] = getChainConnection(updatedPosition, allPositions);
        //
        // const [nextPositionGuid] = updatedPosition.connections.filter(
        //     (connection) => connection.type === 0 && connection.to !== updatedPosition.guid
        // );
        //
        // if (!updatedPosition.connections.length || !nextPositionGuid) {
        //     initNextPositionStartDate = null;
        // } else {
        //     initNextPositionStartDate = allPositions[nextPositionGuid.to]!.planned.start_date;
        //
        //     diffBeforePosition =
        //         moment(initNextPositionStartDate).diff(moment(initEndDate), 'days') - 1;
        // }

        // if (diffBeforePosition - moveDays < 0) {
        //     const diff = Math.abs(diffBeforePosition - moveDays);
        //
        //     const updateNextPosition = getUpdatePositions(chainConnection, diff);
        //
        //     updateNextPosition.forEach((item) => {
        //         updatedPositionsEntities[item.id] = item.changes;
        //     });
        // }

        const updatePositionEntity = {
            [updatedPosition.guid]: updatedPosition,
        };

        updateAll({ ...updatedPositionsEntities, ...updatePositionEntity });
    };

    const decreaseStage = (
        stage: Stage,
        difference: number,
        pickerData: { start: string; end: string; days: string } | null = null
    ) => {
        // let resultPositions = { ...allPositions };

        let positionWithConnection = stagePositions.filter(
            (position) => position.connections.length
        );

        const positionWithoutConnection = stagePositions.filter(
            (position) => !position.connections.length
        );

        let differenceRemainder = difference;

        const isConnection = !!positionWithConnection.length;

        if (!isConnection) {
            let resultPositionNotConnection: Dictionary<Position> = {};

            stagePositions.forEach((position) => {
                resultPositionNotConnection[position.guid] = position;
            });

            let planned: Planned = stage.planned;

            const days_count = +stage.planned.days_count! - difference;

            if (days_count === 1) {
                planned = {
                    days_count: '1',
                    start_date: stage.planned.start_date,
                    end_date: stage.planned.start_date,
                };

                const positionsUpdatedData = stagePositions.map((position) => ({
                    id: position.guid,
                    changes: { ...position, planned },
                }));

                stagePositions
                    .filter((position) => position.planned.days_count)
                    .forEach((position) => {
                        resultPositionNotConnection[position.guid] = {
                            ...resultPositionNotConnection[position.guid]!,
                            planned,
                        };
                    });

                // dispatch(positionActions.updateMany(positionsUpdatedData));

                // TODO повесить сюда updateAll
            } else {
                planned = {
                    ...planned,
                    days_count: `${days_count}`,
                    end_date: moment(planned.start_date!)
                        .add(days_count - 1, 'days')
                        .format(),
                };

                stagePositions.forEach(({ guid }) => {
                    const currentPosition = resultPositionNotConnection[guid]!;

                    if (!currentPosition.planned.start_date) {
                        return;
                    }

                    let endDatePosition = currentPosition.planned.end_date!;
                    let startDatePosition = currentPosition.planned.start_date!;
                    const endDateCharter = planned.end_date!;
                    const startDateCharter = planned.start_date!;

                    if (moment(endDateCharter).isBefore(moment(endDatePosition))) {
                        const diff = moment(endDateCharter).diff(moment(endDatePosition), 'days');

                        endDatePosition = moment(currentPosition.planned.end_date!)
                            .add(-Math.abs(diff), 'days')
                            .format();

                        startDatePosition = moment(currentPosition.planned.start_date!)
                            .add(-Math.abs(diff), 'days')
                            .format();

                        let positionPlanned: Planned;

                        if (moment(startDatePosition).isBefore(moment(startDateCharter))) {
                            positionPlanned = {
                                ...planned,
                            };
                        } else {
                            positionPlanned = {
                                ...currentPosition.planned,
                                start_date: startDatePosition,
                                end_date: endDatePosition,
                            };
                        }

                        resultPositionNotConnection[guid] = {
                            ...currentPosition,
                            planned: positionPlanned,
                        };
                    }
                });

                const updateManyPositions = Object.values(resultPositionNotConnection).map(
                    (position) => ({
                        id: position!.guid,
                        changes: position!,
                    })
                );
                // TODO повесить сюда updateAll

                dispatch(positionActions.updateMany(updateManyPositions));
            }

            // updatePlannedParents(planned, 'charter');
            updateAll(resultPositionNotConnection);

            return;
        }

        const chains = getChains(positionWithConnection, lastLaborPosition.guid)
            .map((chain, index) => ({
                [index]: { chain, length: chain.length },
            }))
            .sort(
                (chainA, chainB) =>
                    Object.values(chainB)[0].length - Object.values(chainA)[0].length
            );

        const longestChain = Object.values(chains[0])[0];

        if (+stage.planned.days_count! - difference < longestChain.length) {
            setIsShowPopup(true);
        }

        differenceRemainder =
            +stage.planned.days_count! - difference < longestChain.length
                ? difference - (longestChain.length - (+stage.planned.days_count! - difference))
                : difference;

        // let endDate = moment(stage.planned.end_date!).add(-difference, 'days').format();
        let daysCount = +stage.planned.days_count! - differenceRemainder;

        let resultPositions: Dictionary<Position> = {};

        [...positionWithConnection, ...positionWithoutConnection].forEach((position) => {
            resultPositions[position.guid] = position;
        });

        chains.forEach((chain) => {
            resultPositions = {
                ...resultPositions,
                ...updateChain(
                    Object.values(chain)[0].chain,
                    resultPositions,
                    stage.planned,
                    differenceRemainder,
                    allPositions,
                    updateConnectionPositions
                ),
            };
        });

        positionWithoutConnection.forEach((position) => {
            resultPositions = {
                ...resultPositions,
                ...updatePositionWithoutConnect(
                    position,
                    resultPositions,
                    stage.planned,
                    differenceRemainder,
                    allPositions
                ),
            };
        });

        updateAll(resultPositions);
    };

    const increaseStageNew = (
        charterForm: { days: string; date: string | null },
        difference: number,
        positionsEntities: Dictionary<Position>
    ) => {
        let updatedPositionsEntities = { ...positionsEntities };

        const positions: Dictionary<Position> = { ...allPositions, ...updatedPositionsEntities };

        const { allPositions: stagePositions } = getStageChildren(positions);

        const endDate = moment(charterForm.date)
            .add(Number(charterForm.days) - 1, 'days')
            .format();

        const filteredPosition = [...stagePositions]
            .filter((position) => position.planned.end_date)
            .sort(
                (positionA, positionB) =>
                    moment(positionA.planned.end_date!).valueOf() -
                    moment(positionB.planned.end_date!).valueOf()
            );

        const lastPositionFiltered = filteredPosition.at(-1)!;

        const start = moment(lastPositionFiltered.planned.start_date!);
        const end = moment(endDate);
        const duration = moment.duration(end.diff(start));
        const days = `${Math.round(+duration.asDays()) + 1}`;

        const planned: Planned = {
            ...lastPositionFiltered.planned,
            days_count: days,
            end_date: endDate,
        };

        const updatedPosition: Position = { ...lastPositionFiltered, planned };

        const moveDays = difference;

        const nextPositions = updatedPosition.connections
            .filter(
                (connection) => connection.type === 0 && updatedPosition.guid === connection.from
            )
            .map((connection) => positions[connection.to]!);

        const chainsConnections = nextPositions.map((position) => [
            position,
            ...getChainConnection(position, positions),
        ]);

        if (chainsConnections.length) {
            chainsConnections.forEach((chain) => {
                const nextPosition = chain.at(0)!;

                const initEndDate = positions[lastPositionFiltered.guid]!.planned.end_date;

                const nextPositionStartDate = nextPosition.planned.start_date!;

                const diffBeforePosition =
                    moment(nextPositionStartDate).diff(moment(initEndDate), 'days') - 1;

                if (diffBeforePosition - moveDays < 0) {
                    const diff = Math.abs(diffBeforePosition - moveDays);

                    const updatePosition = getUpdatePositions(chain, diff);

                    updatedPositionsEntities[updatedPosition!.guid] = updatedPosition;

                    updatePosition.forEach((item) => {
                        updatedPositionsEntities[item.id] = item.changes;
                    });
                }
            });
        }

        const updatePositionEntity = {
            [updatedPosition.guid]: updatedPosition,
        };

        return { ...updatedPositionsEntities, ...updatePositionEntity };
    };

    const moveStage = (stage: Stage, moveDays: number) => {
        const updatedPositionsEntities: Dictionary<Position> = {};

        if (!positionsWithConnection.length) {
            stagePositions.forEach((position) => {
                const planned: Planned = {
                    ...position.planned,
                    start_date: moment(position.planned.start_date!).add(moveDays, 'days').format(),
                    end_date: moment(position.planned.end_date!).add(moveDays, 'days').format(),
                };

                if (position.planned.start_date) {
                    updatedPositionsEntities[position.guid] = { ...position, planned };
                }
            });
            updateAll(updatedPositionsEntities);
            return;
        }

        const chains = getChains(positionsWithConnection, positionsWithConnection.at(-1)!.guid);

        if (moveDays > 0) {
            stagePositions.forEach((position) => {
                const planned: Planned = {
                    ...position.planned,
                    start_date: moment(position.planned.start_date!).add(moveDays, 'days').format(),
                    end_date: moment(position.planned.end_date!).add(moveDays, 'days').format(),
                };
                if (position.planned.start_date) {
                    updatedPositionsEntities[position.guid] = { ...position, planned };
                }
            });

            chains.forEach((chain) => {
                const lastChainPosition = updatedPositionsEntities[chain.at(-1)!.guid]!;

                let chainConnection: Position[] = getChainConnection(
                    lastChainPosition,
                    allPositions
                );

                const nextPositions = lastChainPosition.connections
                    .filter(
                        (connection) =>
                            connection.type === 0 && lastChainPosition.guid === connection.from
                    )
                    .map((connection) => allPositions[connection.to]!);

                const chainsConnections = nextPositions.map((position) => [
                    position,
                    ...getChainConnection(position, allPositions),
                ]);

                if (chainsConnections.length) {
                    chainsConnections.forEach((chain) => {
                        const nextPosition = chain.at(0)!;

                        const initEndDate = allPositions[lastChainPosition.guid]!.planned.end_date;

                        const nextPositionStartDate = nextPosition.planned.start_date!;

                        const diffBeforePosition =
                            moment(nextPositionStartDate).diff(moment(initEndDate), 'days') - 1;

                        if (diffBeforePosition - moveDays < 0) {
                            const diff = Math.abs(diffBeforePosition - moveDays);

                            const updatePosition = getUpdatePositions(chain, diff);

                            updatedPositionsEntities[lastChainPosition!.guid] = lastChainPosition;

                            updatePosition.forEach((item) => {
                                updatedPositionsEntities[item.id] = item.changes;
                            });
                        }
                    });
                }

                // const [nextPositionGuid] = lastChainPosition.connections.filter(
                //     (connection) =>
                //         connection.type === 0 && connection.to !== lastChainPosition.guid
                // );
                //
                // if (!nextPositionGuid) return;
                //
                // const diffBeforePosition =
                //     moment(allPositions[nextPositionGuid.to]!.planned.start_date).diff(
                //         moment(lastChainPosition.planned.end_date),
                //         'days'
                //     ) - 1;
                //
                // if (diffBeforePosition < 0) {
                //     const updateNextPosition = getUpdatePositions(
                //         chainConnection,
                //         Math.abs(diffBeforePosition)
                //     );
                //
                //     updateNextPosition.forEach((item) => {
                //         updatedPositionsEntities[item.id] = item.changes;
                //     });
                // }
            });
        } else {
            const sortedChains = [...chains].sort(
                (positionA, positionB) =>
                    moment(positionA[0].planned.end_date!).valueOf() -
                    moment(positionB[0].planned.end_date!).valueOf()
            );

            const prevPositionConnection = sortedChains[0][0].connections.find(
                (connection) => connection.to === sortedChains[0][0].guid && connection.type === 0
            );

            if (!prevPositionConnection) {
                stagePositions.forEach((position) => {
                    const planned: Planned = {
                        ...position.planned,
                        start_date: moment(position.planned.start_date!)
                            .add(moveDays, 'days')
                            .format(),
                        end_date: moment(position.planned.end_date!).add(moveDays, 'days').format(),
                    };
                    if (position.planned.start_date) {
                        updatedPositionsEntities[position.guid] = { ...position, planned };
                    }
                });
                updateAll(updatedPositionsEntities);
            } else {
                const prevPosition = allPositions[prevPositionConnection.from]!;
                let diffBeforeConnectPosition: number =
                    moment(sortedChains[0][0].planned.start_date).diff(
                        moment(prevPosition.planned.end_date),
                        'days'
                    ) - 1;

                const newMoveDays =
                    diffBeforeConnectPosition + moveDays < 0
                        ? -diffBeforeConnectPosition
                        : moveDays;

                stagePositions.forEach((position) => {
                    const planned: Planned = {
                        ...position.planned,
                        start_date: moment(position.planned.start_date!)
                            .add(newMoveDays, 'days')
                            .format(),
                        end_date: moment(position.planned.end_date!)
                            .add(newMoveDays, 'days')
                            .format(),
                    };
                    if (position.planned.start_date) {
                        updatedPositionsEntities[position.guid] = { ...position, planned };
                    }
                });
                updateAll(updatedPositionsEntities);
            }
        }

        updateAll(updatedPositionsEntities);
    };
    const moveStageNew = (moveDays: number, positionsEntities: Dictionary<Position>) => {
        const updatedPositionsEntities: Dictionary<Position> = { ...positionsEntities };

        if (!positionsWithConnection.length) {
            stagePositions.forEach((position) => {
                const planned: Planned = {
                    ...position.planned,
                    start_date: moment(position.planned.start_date!).add(moveDays, 'days').format(),
                    end_date: moment(position.planned.end_date!).add(moveDays, 'days').format(),
                };

                if (position.planned.start_date) {
                    updatedPositionsEntities[position.guid] = { ...position, planned };
                }
            });
            return updatedPositionsEntities;
        }

        const chains = getChains(positionsWithConnection, positionsWithConnection.at(-1)!.guid);

        if (moveDays > 0) {
            stagePositions.forEach((position) => {
                const planned: Planned = {
                    ...position.planned,
                    start_date: moment(position.planned.start_date!).add(moveDays, 'days').format(),
                    end_date: moment(position.planned.end_date!).add(moveDays, 'days').format(),
                };
                if (position.planned.start_date) {
                    updatedPositionsEntities[position.guid] = { ...position, planned };
                }
            });

            chains.forEach((chain) => {
                const lastChainPosition = updatedPositionsEntities[chain.at(-1)!.guid]!;

                let chainConnection: Position[] = getChainConnection(
                    lastChainPosition,
                    allPositions
                );

                const nextPositions = lastChainPosition.connections
                    .filter(
                        (connection) =>
                            connection.type === 0 && lastChainPosition.guid === connection.from
                    )
                    .map((connection) => allPositions[connection.to]!);

                const chainsConnections = nextPositions.map((position) => [
                    position,
                    ...getChainConnection(position, allPositions),
                ]);

                if (chainsConnections.length) {
                    chainsConnections.forEach((chain) => {
                        const nextPosition = chain.at(0)!;

                        const initEndDate = allPositions[lastChainPosition.guid]!.planned.end_date;

                        const nextPositionStartDate = nextPosition.planned.start_date!;

                        const diffBeforePosition =
                            moment(nextPositionStartDate).diff(moment(initEndDate), 'days') - 1;

                        if (diffBeforePosition - moveDays < 0) {
                            const diff = Math.abs(diffBeforePosition - moveDays);

                            const updatePosition = getUpdatePositions(chain, diff);

                            updatedPositionsEntities[lastChainPosition!.guid] = lastChainPosition;

                            updatePosition.forEach((item) => {
                                updatedPositionsEntities[item.id] = item.changes;
                            });
                        }
                    });
                }
            });
        } else {
            const sortedChains = [...chains].sort(
                (positionA, positionB) =>
                    moment(positionA[0].planned.end_date!).valueOf() -
                    moment(positionB[0].planned.end_date!).valueOf()
            );

            const prevPositionConnection = sortedChains[0][0].connections.find(
                (connection) => connection.to === sortedChains[0][0].guid && connection.type === 0
            );

            if (!prevPositionConnection) {
                stagePositions.forEach((position) => {
                    const planned: Planned = {
                        ...position.planned,
                        start_date: moment(position.planned.start_date!)
                            .add(moveDays, 'days')
                            .format(),
                        end_date: moment(position.planned.end_date!).add(moveDays, 'days').format(),
                    };
                    if (position.planned.start_date) {
                        updatedPositionsEntities[position.guid] = { ...position, planned };
                    }
                });
            } else {
                const prevPosition = allPositions[prevPositionConnection.from]!;
                let diffBeforeConnectPosition: number =
                    moment(sortedChains[0][0].planned.start_date).diff(
                        moment(prevPosition.planned.end_date),
                        'days'
                    ) - 1;

                const newMoveDays =
                    diffBeforeConnectPosition + moveDays < 0
                        ? -diffBeforeConnectPosition
                        : moveDays;

                stagePositions.forEach((position) => {
                    const planned: Planned = {
                        ...position.planned,
                        start_date: moment(position.planned.start_date!)
                            .add(newMoveDays, 'days')
                            .format(),
                        end_date: moment(position.planned.end_date!)
                            .add(newMoveDays, 'days')
                            .format(),
                    };
                    if (position.planned.start_date) {
                        updatedPositionsEntities[position.guid] = { ...position, planned };
                    }
                });
            }
        }

        return updatedPositionsEntities;
    };

    const decreaseStageNew = (difference: number, positionsEntities: Dictionary<Position>) => {
        let updatedPositionsEntities: Dictionary<Position> = { ...positionsEntities };
        const positions: Dictionary<Position> = { ...allPositions, ...updatedPositionsEntities };

        const { allPositions: stagePositions } = getStageChildren(positions);

        let positionWithConnection = stagePositions.filter(
            (position) => position.connections.length
        );

        const positionWithoutConnection = stagePositions.filter(
            (position) => !position.connections.length
        );

        let differenceRemainder = Math.abs(difference);

        const isConnection = !!positionWithConnection.length;

        if (!isConnection) {
            let resultPositionNotConnection: Dictionary<Position> = {};

            stagePositions.forEach((position) => {
                positions[position.guid] = position;
            });

            let planned: Planned = stage.planned;

            const days_count = +stage.planned.days_count! - Math.abs(difference);

            if (days_count === 1) {
                planned = {
                    days_count: '1',
                    start_date: stage.planned.start_date,
                    end_date: stage.planned.start_date,
                };

                stagePositions
                    .filter((position) => position.planned.days_count)
                    .forEach((position) => {
                        resultPositionNotConnection[position.guid] = {
                            ...resultPositionNotConnection[position.guid]!,
                            planned,
                        };
                    });
            } else {
                planned = {
                    ...planned,
                    days_count: `${days_count}`,
                    end_date: moment(planned.start_date!)
                        .add(days_count - 1, 'days')
                        .format(),
                };

                stagePositions.forEach(({ guid }) => {
                    const currentPosition = resultPositionNotConnection[guid]!;

                    if (!currentPosition.planned.start_date) {
                        return;
                    }

                    let endDatePosition = currentPosition.planned.end_date!;
                    let startDatePosition = currentPosition.planned.start_date!;
                    const endDateCharter = planned.end_date!;
                    const startDateCharter = planned.start_date!;

                    if (moment(endDateCharter).isBefore(moment(endDatePosition))) {
                        const diff = moment(endDateCharter).diff(moment(endDatePosition), 'days');

                        endDatePosition = moment(currentPosition.planned.end_date!)
                            .add(-Math.abs(diff), 'days')
                            .format();

                        startDatePosition = moment(currentPosition.planned.start_date!)
                            .add(-Math.abs(diff), 'days')
                            .format();

                        let positionPlanned: Planned;

                        if (moment(startDatePosition).isBefore(moment(startDateCharter))) {
                            positionPlanned = {
                                ...planned,
                            };
                        } else {
                            positionPlanned = {
                                ...currentPosition.planned,
                                start_date: startDatePosition,
                                end_date: endDatePosition,
                            };
                        }

                        resultPositionNotConnection[guid] = {
                            ...currentPosition,
                            planned: positionPlanned,
                        };
                    }
                });

                const updateManyPositions = Object.values(resultPositionNotConnection).map(
                    (position) => ({
                        id: position!.guid,
                        changes: position!,
                    })
                );
                // TODO повесить сюда updateAll

                // dispatch(positionActions.updateMany(updateManyPositions));
            }

            // updatePlannedParents(planned, 'charter');
            // updateAll(resultPositionNotConnection);

            return { ...updatedPositionsEntities, ...resultPositionNotConnection };
        }

        const chains = getChains(positionWithConnection, lastLaborPosition.guid)
            .map((chain, index) => ({
                [index]: { chain, length: chain.length },
            }))
            .sort(
                (chainA, chainB) =>
                    Object.values(chainB)[0].length - Object.values(chainA)[0].length
            );

        const longestChain = Object.values(chains[0])[0];

        if (+stage.planned.days_count! - Math.abs(difference) < longestChain.length) {
            setIsShowPopup(true);
        }

        differenceRemainder =
            +stage.planned.days_count! - Math.abs(difference) < longestChain.length
                ? Math.abs(difference) -
                  (longestChain.length - (+stage.planned.days_count! - Math.abs(difference)))
                : Math.abs(difference);

        // let endDate = moment(stage.planned.end_date!).add(-difference, 'days').format();
        let daysCount = +stage.planned.days_count! - differenceRemainder;

        let resultPositions: Dictionary<Position> = {};

        [...positionWithConnection, ...positionWithoutConnection].forEach((position) => {
            resultPositions[position.guid] = position;
        });

        const sortedPositions = stagePositions
            .filter((position) => position.planned.start_date)
            .sort(
                (positionA, positionB) =>
                    moment(positionA.planned.end_date!).valueOf() -
                    moment(positionB.planned.end_date!).valueOf()
            );

        const start_date = sortedPositions.at(0)!.planned.start_date!;
        const end_date = sortedPositions.at(-1)!.planned.end_date!;
        const days_count = getCountDaysFromDate(start_date, end_date);
        const updatedPlanned: Planned = {
            start_date,
            end_date,
            days_count: String(days_count),
        };

        chains.forEach((chain) => {
            resultPositions = {
                ...resultPositions,
                ...updateChain(
                    Object.values(chain)[0].chain,
                    resultPositions,
                    updatedPlanned,
                    differenceRemainder,
                    positions,
                    updateConnectionPositions
                ),
            };
        });

        positionWithoutConnection.forEach((position) => {
            resultPositions = {
                ...resultPositions,
                ...updatePositionWithoutConnect(
                    position,
                    resultPositions,
                    updatedPlanned,
                    differenceRemainder,
                    positions
                ),
            };
        });

        return { ...updatedPositionsEntities, ...resultPositions };

        // updateAll(resultPositions);
    };

    const onDataPickerStage = (
        stage: Stage,
        pickerData: { start: string; end: string; days: string }
    ) => {
        const stageInitStartDate = stage.planned.start_date!;

        const isNeedMove = pickerData.start !== stage.planned.start_date;
        const isNeedResize = pickerData.days !== stage.planned.days_count;

        const moveDays = moment(pickerData.start).diff(moment(stageInitStartDate), 'days');

        let resultUpdating: Dictionary<Position> = {};

        if (isNeedMove) {
            const moveResult = moveStageNew(moveDays, resultUpdating);
            resultUpdating = { ...resultUpdating, ...moveResult };
        }
        if (isNeedResize) {
            const isDecrease = +stage.planned.days_count! > +pickerData.days;
            const diff = +pickerData.days - +stage.planned.days_count!;

            if (isDecrease) {
                const decreaseResult = decreaseStageNew(diff, resultUpdating);
                resultUpdating = { ...resultUpdating, ...decreaseResult };
            } else {
                const increaseResult = increaseStageNew(
                    { date: pickerData.start, days: pickerData.days },
                    diff,
                    resultUpdating
                );
                resultUpdating = { ...resultUpdating, ...increaseResult };
            }
        }
        updateAll(resultUpdating);
    };

    return {
        resetStage,
        createStage,
        increaseStage,
        decreaseStage,
        onHidePopup,
        moveStage,
        onDataPickerStage,
    };
};
