import React, { FC, useContext } from 'react';
import cls from './DaysModeHeader.module.css';
import classNames from 'classnames';
import { Context } from '../../../../Gant';
import moment from 'moment';

interface DaysModeHeaderProps {}
export const DaysModeHeader: FC<DaysModeHeaderProps> = () => {
    const { days } = useContext(Context);

    const nowDate = moment().format('DD.MM.YYYY');

    return (
        <div className={classNames(cls.DaysModeHeader)}>
            {days.map((month) => (
                <div className={classNames(cls.monthWrapper)} key={month.title}>
                    <div className={classNames(cls.month)}>{month.title}</div>
                    <div className={classNames(cls.days)}>
                        {month.days.map((day) => (
                            <div
                                key={day.day}
                                data-date-now={day.date === nowDate}
                                className={classNames(cls.day, {
                                    [cls.weekend]: day.isWeekend,
                                    [cls.nowDate]: day.date === nowDate,
                                })}
                            >
                                {day.day}
                            </div>
                        ))}
                    </div>
                </div>
            ))}
        </div>
    );
};
