import { useSelector } from 'react-redux';
import { positionActions, positionSelector } from '../store/reducers/positionSlice/positionSlice';
import { charterActions } from '../store/reducers/charterSlice/charterSlice';
import { useAppDispatch } from './redux';
import {
    Connections,
    Planned,
    Position,
} from '../store/reducers/positionSlice/positionSlice.types';
import { Charter } from '../store/reducers/charterSlice/charterSlice.types';
import { stageSelector } from '../store/reducers/stageSlice/stageSlice';
import { getGantData } from '../store/reducers/gantSlice/selectors/getGantData';
import moment from 'moment/moment';
import { useUpdateConnectionPositions } from './useUpdateConnectionPositions';
import { Dictionary } from '@reduxjs/toolkit';
import { getCountDaysFromDate, getUpdatePositions } from '../helpers/getCountDays';
import { getLongPositions } from '../helpers/getLongPositions';
import { useContext } from 'react';
import { getChains } from '../helpers/getChains';
import { updateChain } from '../helpers/updateChain';
import { updatePositionWithoutConnect } from '../helpers/updatePositionWithoutConnect';
import { useUpdateAll } from './useUpdateAll';
import { createWaterfall } from '../helpers/createWaterfall';
import { Context } from '../components/Gant/Gant';
import { getChainConnection } from '../helpers/getChainConnection';

export const useEditCharter = (charter: Charter) => {
    const dispatch = useAppDispatch();
    const allPositions = useSelector(positionSelector.selectEntities);
    const positionsEntities = useSelector(positionSelector.selectEntities);
    const allStage = useSelector(stageSelector.selectAll);
    const { setIsShowPopup, isPlanFact } = useContext(Context);

    const parentStage = allStage.find((stage) => stage.positions.includes(charter.guid));

    const { updateAll } = useUpdateAll(parentStage!);

    const { updateConnectionPositions } = useUpdateConnectionPositions();
    const lastLaborPositionGuid = charter.children
        .filter((guid) => allPositions[guid]!.type === 'labor')
        .filter((guid) => allPositions[guid]!.planned.days_count)
        .sort(
            (positionA, positionB) =>
                moment(allPositions[positionA]!.planned.end_date!).valueOf() -
                moment(allPositions[positionB]!.planned.end_date!).valueOf()
        )
        .at(-1)!;
    const { settings } = useSelector(getGantData);

    const laborCount = charter.children.filter(
        (positionGuid) => allPositions[positionGuid]!.type === 'labor'
    ).length;

    const charterPositions = charter.children.map((guid) => allPositions[guid]!);

    const positionsWithConnection = charterPositions
        .filter((position) => !!position.connections.length)
        .sort(
            (positionA, positionB) =>
                moment(positionA.planned.end_date!).valueOf() -
                moment(positionB.planned.end_date!).valueOf()
        );

    const onHidePopup = () => setIsShowPopup(false);

    const resetCharter = (charter: Charter) => {
        const resetPlanned: Planned = {
            days_count: null,
            start_date: null,
            end_date: null,
        };

        const resetConnection: Connections[] = [];

        // const updatedCharter: Charter = { ...charter, planned: resetPlanned };
        // const updatedPositions: { id: string; changes: Position }[] = [];

        let resultPositionsEntities: Dictionary<Position> = { ...allPositions };
        charter.children.forEach((positionGuid) => {
            const currentPosition = resultPositionsEntities[positionGuid]!;

            if (currentPosition.connections.length) {
                currentPosition.connections.forEach((connection) => {
                    if (connection.to === currentPosition.guid) {
                        const connectionsFrom = resultPositionsEntities[
                            connection.from
                        ]!.connections.filter((connectionFrom) => {
                            return connectionFrom.to !== connection.to;
                        });

                        resultPositionsEntities[connection.from] = {
                            ...resultPositionsEntities[connection.from]!,
                            connections: connectionsFrom,
                        };
                    } else {
                        const connectionsTo = resultPositionsEntities[
                            connection.to
                        ]!.connections.filter(
                            (connectionTo) => connectionTo.from !== connection.from
                        );

                        resultPositionsEntities[connection.to] = {
                            ...resultPositionsEntities[connection.to]!,
                            connections: connectionsTo,
                        };
                    }
                });
            }

            const updatedPosition: Position = {
                ...resultPositionsEntities[positionGuid]!,
                planned: resetPlanned,
                connections: resetConnection,
            };
            resultPositionsEntities[positionGuid] = updatedPosition;

            // updatedPositions.push({ id: positionGuid, changes: updatedPosition });
        });

        // let updatedPositionsEntities: Dictionary<Position> = {};
        //
        // updatedPositions.forEach(({ id, changes }) => {
        //     updatedPositionsEntities[id] = changes;
        // });

        updateAll(resultPositionsEntities);
        // updateAll(updatedPositionsEntities);

        dispatch(
            charterActions.updateOne({
                id: charter.guid,
                changes: { ...charter, planned: resetPlanned },
            })
        );
    };

    const crateCharter = (charter: Charter, charterForm: { days: string; date: string | null }) => {
        let planned: Planned;

        const charterPositions = charter.children.map((guid) => allPositions[guid]!);
        const charterLabor = charterPositions.filter((position) => position.type === 'labor');
        const charterMHD = charterPositions.filter((position) => position.type !== 'labor');

        const lastLaborPosition = charterLabor
            .sort(
                (positionA, positionB) =>
                    moment(positionA.planned.end_date!).valueOf() -
                    moment(positionB.planned.end_date!).valueOf()
            )
            .at(-1)!;

        if (!charterLabor.length) {
            let updatedPositionsEntities: Dictionary<Position> = {};

            const start_date = charterForm.date
                ? charterForm.date
                : moment().hour(0).minute(0).seconds(0).format();

            const end_date = moment(start_date)
                .add(+charterForm.days - 1, 'days')
                .format();

            charterMHD.forEach((position) => {
                updatedPositionsEntities[position.guid] = {
                    ...position,
                    planned: {
                        start_date,
                        end_date,
                        days_count: charterForm.days,
                    },
                };
            });

            updateAll(updatedPositionsEntities);
            return;
        }

        if (Number(charterForm.days) < charterLabor.length) {
            setIsShowPopup(true);
        }

        const resultDays =
            Number(charterForm.days) < laborCount ? `${laborCount}` : charterForm.days;

        let currentDate = !!charterForm.date
            ? charterForm.date
            : moment().hour(0).minute(0).seconds(0).format();

        planned = {
            days_count: resultDays,
            start_date: currentDate,
            end_date: moment()
                .hour(0)
                .minute(0)
                .seconds(0)
                .add(Number(resultDays) - 1, 'days')
                .format(),
        };

        const updatedCharter = { ...charter, planned };

        const { long, remainder } = getLongPositions(laborCount, +resultDays);

        let mhdDaysCount =
            Number(updatedCharter.planned.days_count) < +settings.daysMHD
                ? Number(updatedCharter.planned.days_count)
                : settings.daysMHD;

        const plannedMhd: Planned = {
            start_date: currentDate,
            end_date: moment(currentDate)
                .add(+mhdDaysCount - 1, 'days')
                .format(),
            days_count: `${mhdDaysCount}`,
        };

        const updatedEntities = createWaterfall(
            charterPositions,
            currentDate,
            positionsEntities,
            settings,
            lastLaborPosition.guid,
            plannedMhd,
            remainder,
            long
        );

        updateAll(updatedEntities);
    };

    const increaseCharter = (
        charter: Charter,
        charterForm: { days: string; date: string | null },
        difference: number,
        pickerData: { start: string; end: string; days: string } | null = null
    ) => {
        let updatedPositionsEntities = { ...allPositions };

        const charterPositions = charter.children.map((guid) => allPositions[guid]!);
        const charterLabor = charterPositions.filter((position) => position.type === 'labor');
        const charterMHD = charterPositions.filter((position) => position.type !== 'labor');
        const lastPositionGuid = charterLabor.length
            ? lastLaborPositionGuid
            : charterMHD.at(-1)!.guid;

        if (pickerData === null) {
            const endDate = moment(charterForm.date)
                .add(Number(charterForm.days) - 1, 'days')
                .format();

            const start = moment(allPositions[lastPositionGuid]!.planned.start_date!);
            const end = moment(endDate);
            const duration = moment.duration(end.diff(start));
            const days = `${Math.round(+duration.asDays()) + 1}`;

            const planned: Planned = {
                ...allPositions[lastPositionGuid]!.planned,
                days_count: days,
                end_date: endDate,
            };

            const updatedPosition: Position = { ...allPositions[lastPositionGuid]!, planned };

            const moveDays = +charterForm.days - +charter.planned.days_count!;

            const nextPositions = updatedPosition.connections
                .filter(
                    (connection) =>
                        connection.type === 0 && updatedPosition.guid === connection.from
                )
                .map((connection) => allPositions[connection.to]!);

            const chainsConnections = nextPositions.map((position) => [
                position,
                ...getChainConnection(position, allPositions),
            ]);

            if (chainsConnections.length) {
                chainsConnections.forEach((chain) => {
                    const nextPosition = chain.at(0)!;

                    const initEndDate = allPositions[lastPositionGuid]!.planned.end_date;

                    const nextPositionStartDate = nextPosition.planned.start_date!;

                    const diffBeforePosition =
                        moment(nextPositionStartDate).diff(moment(initEndDate), 'days') - 1;

                    if (diffBeforePosition - moveDays < 0) {
                        const diff = Math.abs(diffBeforePosition - moveDays);

                        const updatePosition = getUpdatePositions(chain, diff);

                        updatedPositionsEntities[updatedPosition!.guid] = updatedPosition;

                        updatePosition.forEach((item) => {
                            updatedPositionsEntities[item.id] = item.changes;
                        });
                    }
                });
            }

            const updatePositionEntity = {
                [updatedPosition.guid]: updatedPosition,
            };

            updateAll({ ...updatedPositionsEntities, ...updatePositionEntity });
        } else {
            const days_count = +allPositions[lastPositionGuid]!.planned.days_count! + difference;
            const planned: Planned = {
                days_count: `${days_count}`,
                end_date: moment(pickerData.end).hour(0).minute(0).seconds(0).format(),
                start_date: moment(pickerData.end)
                    .hour(0)
                    .minute(0)
                    .seconds(0)
                    .add(-days_count + 1, 'days')
                    .format(),
            };
        }
    };

    const decreaseCharter = (charter: Charter, difference: number) => {
        const charterPositions = charter.children.map((guid) => allPositions[guid]!);
        const charterLabor = charterPositions.filter((position) => position.type === 'labor');
        // const charterMHD = charterPositions.filter((position) => position.type !== 'labor');

        let positionWithConnection = charterPositions.filter(
            (position) => position.connections.length
        );

        const positionWithoutConnection = charterLabor.filter(
            (position) => !position.connections.length
        );

        const chains = getChains(positionWithConnection, lastLaborPositionGuid, allPositions)
            .map((chain, index) => ({
                [index]: { chain, length: chain.length },
            }))
            .sort(
                (chainA, chainB) =>
                    Object.values(chainB)[0].length - Object.values(chainA)[0].length
            );

        let differenceRemainder = difference;

        const isConnection = !!chains.length;

        if (!isConnection) {
            let resultPositionNotConnection: Dictionary<Position> = {};

            charter.children.forEach((guid) => {
                resultPositionNotConnection[guid] = allPositions[guid]!;
            });

            let planned: Planned = charter.planned;

            const days_count = +charter.planned.days_count! - difference;

            if (days_count === 1) {
                planned = {
                    days_count: '1',
                    start_date: charter.planned.start_date,
                    end_date: charter.planned.start_date,
                };

                charter.children
                    .filter((guid) => allPositions[guid]?.planned.days_count)
                    .forEach((guid) => {
                        resultPositionNotConnection[guid] = {
                            ...resultPositionNotConnection[guid]!,
                            planned,
                        };
                    });
            } else {
                planned = {
                    ...planned,
                    days_count: `${days_count}`,
                    end_date: moment(planned.start_date!)
                        .add(days_count - 1, 'days')
                        .format(),
                };

                charter.children.forEach((guid) => {
                    const currentPosition = resultPositionNotConnection[guid]!;

                    if (!currentPosition.planned.start_date) {
                        return;
                    }

                    let endDatePosition = currentPosition.planned.end_date!;
                    let startDatePosition = currentPosition.planned.start_date!;
                    const endDateCharter = planned.end_date!;
                    const startDateCharter = planned.start_date!;

                    if (moment(endDateCharter).isBefore(moment(endDatePosition))) {
                        const diff = moment(endDateCharter).diff(moment(endDatePosition), 'days');

                        endDatePosition = moment(currentPosition.planned.end_date!)
                            .add(-Math.abs(diff), 'days')
                            .format();

                        startDatePosition = moment(currentPosition.planned.start_date!)
                            .add(-Math.abs(diff), 'days')
                            .format();

                        let positionPlanned: Planned;

                        if (moment(startDatePosition).isBefore(moment(startDateCharter))) {
                            positionPlanned = {
                                ...planned,
                            };
                        } else {
                            positionPlanned = {
                                ...currentPosition.planned,
                                start_date: startDatePosition,
                                end_date: endDatePosition,
                            };
                        }

                        resultPositionNotConnection[guid] = {
                            ...currentPosition,
                            planned: positionPlanned,
                        };
                    }
                });

                const updateManyPositions = Object.values(resultPositionNotConnection).map(
                    (position) => ({
                        id: position!.guid,
                        changes: position!,
                    })
                );

                dispatch(positionActions.updateMany(updateManyPositions));

                dispatch(
                    charterActions.updateOne({
                        id: charter.guid,
                        changes: { ...charter, planned },
                    })
                );
            }

            updateAll(resultPositionNotConnection);

            return;
        }
        const longestChain = Object.values(chains[0])[0];

        differenceRemainder =
            +charter.planned.days_count! - difference < longestChain.length
                ? difference - (longestChain.length - (+charter.planned.days_count! - difference))
                : difference;

        let daysCount = +charter.planned.days_count! - difference;

        if (daysCount < longestChain.length) {
            setIsShowPopup(true);
        }

        let resultPositions: Dictionary<Position> = {};

        [...positionWithConnection, ...positionWithoutConnection].forEach((position) => {
            resultPositions[position.guid] = position;
        });

        chains.forEach((chain) => {
            resultPositions = {
                ...resultPositions,
                ...updateChain(
                    Object.values(chain)[0].chain,
                    resultPositions,
                    charter.planned,
                    differenceRemainder,
                    allPositions,
                    updateConnectionPositions
                ),
            };
        });

        positionWithoutConnection.forEach((position) => {
            resultPositions = {
                ...resultPositions,
                ...updatePositionWithoutConnect(
                    position,
                    resultPositions,
                    charter.planned,
                    differenceRemainder,
                    allPositions
                ),
            };
        });

        updateAll(resultPositions);
    };

    const increaseCharterNew = (
        charterForm: { days: string; date: string | null },
        difference: number,
        positionsEntities: Dictionary<Position>
    ): Dictionary<Position> => {
        let updatedPositionsEntities: Dictionary<Position> = positionsEntities;

        const positions: Dictionary<Position> = { ...allPositions, ...updatedPositionsEntities };

        const charterPositions = charter.children.map((guid) => positions[guid]!);
        const charterLabor = charterPositions.filter((position) => position.type === 'labor');
        const charterMHD = charterPositions.filter((position) => position.type !== 'labor');
        const lastPositionGuid = charterLabor.length
            ? lastLaborPositionGuid
            : charterMHD.at(-1)!.guid;

        const endDate = moment(charterForm.date)
            .add(Number(charterForm.days) - 1, 'days')
            .format();

        const start = moment(positions[lastPositionGuid]!.planned.start_date!);
        const end = moment(endDate);
        const duration = moment.duration(end.diff(start));
        const days = `${Math.round(+duration.asDays()) + 1}`;

        const days_count = +positions[lastPositionGuid]!.planned.days_count! + difference;

        const planned: Planned = {
            ...positions[lastPositionGuid]!.planned,
            days_count: `${days_count}`,
            end_date: moment(positions[lastPositionGuid]!.planned.start_date)
                .add(days_count - 1, 'days')
                .format(),
        };

        const updatedPosition: Position = { ...positions[lastPositionGuid]!, planned };

        const moveDays = difference;

        const nextPositions = updatedPosition.connections
            .filter(
                (connection) => connection.type === 0 && updatedPosition.guid === connection.from
            )
            .map((connection) => allPositions[connection.to]!);

        const chainsConnections = nextPositions.map((position) => [
            position,
            ...getChainConnection(position, allPositions),
        ]);

        if (chainsConnections.length) {
            chainsConnections.forEach((chain) => {
                const nextPosition = chain.at(0)!;

                const initEndDate = positions[lastPositionGuid]!.planned.end_date;

                const nextPositionStartDate = nextPosition.planned.start_date!;

                const diffBeforePosition =
                    moment(nextPositionStartDate).diff(moment(initEndDate), 'days') - 1;

                if (diffBeforePosition - moveDays < 0) {
                    const diff = Math.abs(diffBeforePosition - moveDays);

                    const updatePosition = getUpdatePositions(chain, diff);

                    updatedPositionsEntities[updatedPosition!.guid] = updatedPosition;

                    updatePosition.forEach((item) => {
                        updatedPositionsEntities[item.id] = item.changes;
                    });
                }
            });
        }

        const updatePositionEntity = {
            [updatedPosition.guid]: updatedPosition,
        };

        return { ...updatedPositionsEntities, ...updatePositionEntity };

        // updateAll({ ...updatedPositionsEntities, ...updatePositionEntity });
    };

    const decreaseCharterNew = (difference: number, positionsEntities: Dictionary<Position>) => {
        let updatedPositionsEntities: Dictionary<Position> = positionsEntities;

        const positions: Dictionary<Position> = { ...allPositions, ...updatedPositionsEntities };

        const charterPositions = charter.children.map((guid) => positions[guid]!);

        const charterLabor = charterPositions.filter((position) => position.type === 'labor');
        const charterMHD = charterPositions.filter((position) => position.type !== 'labor');

        let positionWithConnection = charterPositions.filter(
            (position) => position.connections.length
        );

        const positionWithoutConnection = charterLabor.filter(
            (position) => !position.connections.length
        );

        const chains = getChains(positionWithConnection, lastLaborPositionGuid)
            .map((chain, index) => ({
                [index]: { chain, length: chain.length },
            }))
            .sort(
                (chainA, chainB) =>
                    Object.values(chainB)[0].length - Object.values(chainA)[0].length
            );

        let differenceRemainder = Math.abs(difference);

        const isConnection = !!chains.length;

        if (!isConnection) {
            let resultPositionNotConnection: Dictionary<Position> = {};

            charter.children.forEach((guid) => {
                resultPositionNotConnection[guid] = positions[guid]!;
            });

            let planned: Planned = charter.planned;

            const days_count = +charter.planned.days_count! - Math.abs(difference);

            if (days_count === 1) {
                planned = {
                    days_count: '1',
                    start_date: charter.planned.start_date,
                    end_date: charter.planned.start_date,
                };

                charter.children
                    .filter((guid) => positions[guid]?.planned.days_count)
                    .forEach((guid) => {
                        resultPositionNotConnection[guid] = {
                            ...resultPositionNotConnection[guid]!,
                            planned,
                        };
                    });
            } else {
                planned = {
                    ...planned,
                    days_count: `${days_count}`,
                    end_date: moment(planned.start_date!)
                        .add(days_count - 1, 'days')
                        .format(),
                };

                charter.children.forEach((guid) => {
                    const currentPosition = resultPositionNotConnection[guid]!;

                    if (!currentPosition.planned.start_date) {
                        return updatedPositionsEntities;
                    }

                    let endDatePosition = currentPosition.planned.end_date!;
                    let startDatePosition = currentPosition.planned.start_date!;
                    const endDateCharter = planned.end_date!;
                    const startDateCharter = planned.start_date!;

                    if (moment(endDateCharter).isBefore(moment(endDatePosition))) {
                        const diff = moment(endDateCharter).diff(moment(endDatePosition), 'days');

                        endDatePosition = moment(currentPosition.planned.end_date!)
                            .add(-Math.abs(diff), 'days')
                            .format();

                        startDatePosition = moment(currentPosition.planned.start_date!)
                            .add(-Math.abs(diff), 'days')
                            .format();

                        let positionPlanned: Planned;

                        if (moment(startDatePosition).isBefore(moment(startDateCharter))) {
                            positionPlanned = {
                                ...planned,
                            };
                        } else {
                            positionPlanned = {
                                ...currentPosition.planned,
                                start_date: startDatePosition,
                                end_date: endDatePosition,
                            };
                        }

                        resultPositionNotConnection[guid] = {
                            ...currentPosition,
                            planned: positionPlanned,
                        };
                    }
                });

                const updateManyPositions = Object.values(resultPositionNotConnection).map(
                    (position) => ({
                        id: position!.guid,
                        changes: position!,
                    })
                );

                // dispatch(positionActions.updateMany(updateManyPositions));

                dispatch(
                    charterActions.updateOne({
                        id: charter.guid,
                        changes: { ...charter, planned },
                    })
                );
            }

            // updateAll(resultPositionNotConnection);

            return { ...updatedPositionsEntities, ...resultPositionNotConnection };
        }

        const longestChain = Object.values(chains[0])[0];

        differenceRemainder =
            +charter.planned.days_count! - difference < longestChain.length
                ? Math.abs(difference) -
                  (longestChain.length - (+charter.planned.days_count! - Math.abs(difference)))
                : Math.abs(difference);

        let daysCount = +charter.planned.days_count! - Math.abs(difference);

        if (daysCount < longestChain.length) {
            setIsShowPopup(true);
        }

        let resultPositions: Dictionary<Position> = {};

        [...positionWithConnection, ...positionWithoutConnection].forEach((position) => {
            resultPositions[position.guid] = position;
        });

        const sortedPositions = charterPositions
            .filter((position) => position.planned.start_date)
            .sort(
                (positionA, positionB) =>
                    moment(positionA.planned.end_date!).valueOf() -
                    moment(positionB.planned.end_date!).valueOf()
            );

        const start_date = sortedPositions.at(0)!.planned.start_date!;
        const end_date = sortedPositions.at(-1)!.planned.end_date!;
        const days_count = getCountDaysFromDate(start_date, end_date);
        const updatedPlanned: Planned = {
            start_date,
            end_date,
            days_count: String(days_count),
        };

        chains.forEach((chain) => {
            resultPositions = {
                ...resultPositions,
                ...updateChain(
                    Object.values(chain)[0].chain,
                    resultPositions,
                    updatedPlanned,
                    differenceRemainder,
                    positions,
                    updateConnectionPositions
                ),
            };
        });

        positionWithoutConnection.forEach((position) => {
            resultPositions = {
                ...resultPositions,
                ...updatePositionWithoutConnect(
                    position,
                    resultPositions,
                    updatedPlanned,
                    differenceRemainder,
                    positions
                ),
            };
        });

        return { ...updatedPositionsEntities, ...resultPositions };

        // updateAll(resultPositions);
    };

    const moveCharter = (charter: Charter, moveDays: number) => {
        const updatedPositionsEntities: Dictionary<Position> = {};

        if (!positionsWithConnection.length) {
            charterPositions.forEach((position) => {
                const planned: Planned = {
                    ...position.planned,
                    start_date: moment(position.planned.start_date!).add(moveDays, 'days').format(),
                    end_date: moment(position.planned.end_date!).add(moveDays, 'days').format(),
                };

                if (position.planned.start_date) {
                    updatedPositionsEntities[position.guid] = { ...position, planned };
                }
            });
            updateAll(updatedPositionsEntities);
            return;
        }

        const chains = getChains(positionsWithConnection, positionsWithConnection.at(-1)!.guid);

        if (moveDays > 0) {
            charterPositions.forEach((position) => {
                const planned: Planned = {
                    ...position.planned,
                    start_date: moment(position.planned.start_date!).add(moveDays, 'days').format(),
                    end_date: moment(position.planned.end_date!).add(moveDays, 'days').format(),
                };
                if (position.planned.start_date) {
                    updatedPositionsEntities[position.guid] = { ...position, planned };
                }
            });

            chains.forEach((chain) => {
                const lastChainPosition = updatedPositionsEntities[chain.at(-1)!.guid]!;

                const nextPositions = lastChainPosition.connections
                    .filter(
                        (connection) =>
                            connection.type === 0 && lastChainPosition.guid === connection.from
                    )
                    .map((connection) => allPositions[connection.to]!);

                const chainsConnections = nextPositions.map((position) => [
                    position,
                    ...getChainConnection(position, allPositions),
                ]);

                if (chainsConnections.length) {
                    chainsConnections.forEach((chain) => {
                        const nextPosition = chain.at(0)!;

                        const initEndDate = allPositions[lastChainPosition.guid]!.planned.end_date;

                        const nextPositionStartDate = nextPosition.planned.start_date!;

                        const diffBeforePosition =
                            moment(nextPositionStartDate).diff(moment(initEndDate), 'days') - 1;

                        if (diffBeforePosition - moveDays < 0) {
                            const diff = Math.abs(diffBeforePosition - moveDays);

                            const updatePosition = getUpdatePositions(chain, diff);

                            updatedPositionsEntities[lastChainPosition!.guid] = lastChainPosition;

                            updatePosition.forEach((item) => {
                                updatedPositionsEntities[item.id] = item.changes;
                            });
                        }
                    });
                }
            });
        } else {
            const sortedChains = [...chains].sort(
                (positionA, positionB) =>
                    moment(positionA[0].planned.end_date!).valueOf() -
                    moment(positionB[0].planned.end_date!).valueOf()
            );

            const prevPositionConnection = sortedChains[0][0].connections.find(
                (connection) => connection.to === sortedChains[0][0].guid && connection.type === 0
            );

            if (!prevPositionConnection) {
                charterPositions.forEach((position) => {
                    const planned: Planned = {
                        ...position.planned,
                        start_date: moment(position.planned.start_date!)
                            .add(moveDays, 'days')
                            .format(),
                        end_date: moment(position.planned.end_date!).add(moveDays, 'days').format(),
                    };
                    if (position.planned.start_date) {
                        updatedPositionsEntities[position.guid] = { ...position, planned };
                    }
                });
                updateAll(updatedPositionsEntities);
            } else {
                const prevPosition = allPositions[prevPositionConnection.from]!;
                let diffBeforeConnectPosition: number =
                    moment(sortedChains[0][0].planned.start_date).diff(
                        moment(prevPosition.planned.end_date),
                        'days'
                    ) - 1;

                const newMoveDays =
                    diffBeforeConnectPosition + moveDays < 0
                        ? -diffBeforeConnectPosition
                        : moveDays;

                charterPositions.forEach((position) => {
                    const planned: Planned = {
                        ...position.planned,
                        start_date: moment(position.planned.start_date!)
                            .add(newMoveDays, 'days')
                            .format(),
                        end_date: moment(position.planned.end_date!)
                            .add(newMoveDays, 'days')
                            .format(),
                    };
                    if (position.planned.start_date) {
                        updatedPositionsEntities[position.guid] = { ...position, planned };
                    }
                });
                updateAll(updatedPositionsEntities);
            }
        }

        updateAll(updatedPositionsEntities);
    };

    const moveCharterNew = (moveDays: number, positionsEntities: Dictionary<Position>) => {
        const updatedPositionsEntities: Dictionary<Position> = positionsEntities;

        if (!positionsWithConnection.length) {
            charterPositions.forEach((position) => {
                const planned: Planned = {
                    ...position.planned,
                    start_date: moment(position.planned.start_date!).add(moveDays, 'days').format(),
                    end_date: moment(position.planned.end_date!).add(moveDays, 'days').format(),
                };

                if (position.planned.start_date) {
                    updatedPositionsEntities[position.guid] = { ...position, planned };
                }
            });
            // updateAll(updatedPositionsEntities);
            return updatedPositionsEntities;
        }

        const chains = getChains(positionsWithConnection, positionsWithConnection.at(-1)!.guid);

        if (moveDays > 0) {
            charterPositions.forEach((position) => {
                const planned: Planned = {
                    ...position.planned,
                    start_date: moment(position.planned.start_date!).add(moveDays, 'days').format(),
                    end_date: moment(position.planned.end_date!).add(moveDays, 'days').format(),
                };
                if (position.planned.start_date) {
                    updatedPositionsEntities[position.guid] = { ...position, planned };
                }
            });

            chains.forEach((chain) => {
                const lastChainPosition = updatedPositionsEntities[chain.at(-1)!.guid]!;

                const nextPositions = lastChainPosition.connections
                    .filter(
                        (connection) =>
                            connection.type === 0 && lastChainPosition.guid === connection.from
                    )
                    .map((connection) => allPositions[connection.to]!);

                const chainsConnections = nextPositions.map((position) => [
                    position,
                    ...getChainConnection(position, allPositions),
                ]);

                if (chainsConnections.length) {
                    chainsConnections.forEach((chain) => {
                        const nextPosition = chain.at(0)!;

                        const initEndDate = allPositions[lastChainPosition.guid]!.planned.end_date;

                        const nextPositionStartDate = nextPosition.planned.start_date!;

                        const diffBeforePosition =
                            moment(nextPositionStartDate).diff(moment(initEndDate), 'days') - 1;

                        if (diffBeforePosition - moveDays < 0) {
                            const diff = Math.abs(diffBeforePosition - moveDays);

                            const updatePosition = getUpdatePositions(chain, diff);

                            updatedPositionsEntities[lastChainPosition!.guid] = lastChainPosition;

                            updatePosition.forEach((item) => {
                                updatedPositionsEntities[item.id] = item.changes;
                            });
                        }
                    });
                }
            });
        } else {
            const sortedChains = [...chains].sort(
                (positionA, positionB) =>
                    moment(positionA[0].planned.end_date!).valueOf() -
                    moment(positionB[0].planned.end_date!).valueOf()
            );

            const prevPositionConnection = sortedChains[0][0].connections.find(
                (connection) => connection.to === sortedChains[0][0].guid && connection.type === 0
            );

            if (!prevPositionConnection) {
                charterPositions.forEach((position) => {
                    const planned: Planned = {
                        ...position.planned,
                        start_date: moment(position.planned.start_date!)
                            .add(moveDays, 'days')
                            .format(),
                        end_date: moment(position.planned.end_date!).add(moveDays, 'days').format(),
                    };
                    if (position.planned.start_date) {
                        updatedPositionsEntities[position.guid] = { ...position, planned };
                    }
                });
                // updateAll(updatedPositionsEntities);
            } else {
                const prevPosition = allPositions[prevPositionConnection.from]!;
                let diffBeforeConnectPosition: number =
                    moment(sortedChains[0][0].planned.start_date).diff(
                        moment(prevPosition.planned.end_date),
                        'days'
                    ) - 1;

                const newMoveDays =
                    diffBeforeConnectPosition + moveDays < 0
                        ? -diffBeforeConnectPosition
                        : moveDays;

                charterPositions.forEach((position) => {
                    const planned: Planned = {
                        ...position.planned,
                        start_date: moment(position.planned.start_date!)
                            .add(newMoveDays, 'days')
                            .format(),
                        end_date: moment(position.planned.end_date!)
                            .add(newMoveDays, 'days')
                            .format(),
                    };
                    if (position.planned.start_date) {
                        updatedPositionsEntities[position.guid] = { ...position, planned };
                    }
                });
                // updateAll(updatedPositionsEntities);
            }
        }

        return updatedPositionsEntities;

        // updateAll(updatedPositionsEntities);
    };

    const onDataPickerCharter = (
        charter: Charter,
        pickerData: { start: string; end: string; days: string }
    ) => {
        const charterInitStartDate = charter.planned.start_date!;

        const isNeedMove = pickerData.start !== charter.planned.start_date;
        const isNeedResize = pickerData.days !== charter.planned.days_count;

        const moveDays = moment(pickerData.start).diff(moment(charterInitStartDate), 'days');

        let resultUpdating: Dictionary<Position> = {};

        if (isNeedMove) {
            const moveResult = moveCharterNew(moveDays, resultUpdating);
            resultUpdating = { ...resultUpdating, ...moveResult };
        }

        if (isNeedResize) {
            const isDecrease = +charter.planned.days_count! > +pickerData.days;
            const diff = +pickerData.days - +charter.planned.days_count!;

            if (isDecrease) {
                const decreaseResult = decreaseCharterNew(diff, resultUpdating);
                resultUpdating = { ...resultUpdating, ...decreaseResult };
            } else {
                const increaseResult = increaseCharterNew(
                    { date: pickerData.start, days: pickerData.days },
                    diff,
                    resultUpdating
                );
                resultUpdating = { ...resultUpdating, ...increaseResult };
            }
        }

        updateAll(resultUpdating);
    };

    return {
        resetCharter,
        crateCharter,
        increaseCharter,
        decreaseCharter,
        onHidePopup,
        moveCharter,
        onDataPickerCharter,
    };
};
