import React, { FC, useContext, useEffect, useState } from 'react';
import cls from './PositionIItem.module.css';
import classNames from 'classnames';
import { Connections, Position } from '../../../store/reducers/positionSlice/positionSlice.types';
import { Context } from '../../Gant/Gant';
import { ItemResizer } from '../ItemResizer/ItemResizer';
import { ItemMover } from '../ItemMover/ItemMover';
import { PositionCircle } from '../PositionCircle/PositionCircle';
import { useSelector } from 'react-redux';
import { getConnectionsData } from '../../../store/reducers/connectionSlice/selectors/getConnectionsData';
import { ConnectionArrow } from '../ConnectionArrow/ConnectionArrow';
import $ from 'jquery';
import { getGantData } from '../../../store/reducers/gantSlice/selectors/getGantData';
import { useResizePosition } from '../../../hooks/useResizePosition';
import { useMovePosition } from '../../../hooks/useMovePosition';
import { PlanFactPositionItem } from './PlanFactPositionItem';
import { getMinWidth } from '../../../hooks/getMinWidth';

interface StageItemProps {
    position?: Position;
    isPlanFact: boolean;
}

interface Coordinates {
    height: number;
    width: number;
    guid: string;
    type: number;
    positionWidth: number;
    connection: Connections;
}
export const PositionIItem: FC<StageItemProps> = ({ position, isPlanFact }) => {
    const { activeTab, isOpenCharters } = useContext(Context);

    const connection = useSelector(getConnectionsData);
    const { chainPositionMove, settings } = useSelector(getGantData);

    const [coordinates, setCoordinates] = useState<Coordinates[]>([]);

    const { positionX, handleMouseMove, handleMouseUp, needMoveUpdate } = useMovePosition(
        position!,
        activeTab
    );

    const { elWidth, changeWidth, onItemChange, needResizeUpdate } = useResizePosition(
        activeTab,
        position!
    );

    useEffect(() => {
        if (position?.connections.length) {
            const newCoordinates: Coordinates[] = [];

            for (const connection of position.connections) {
                if (connection.to !== position.guid) {
                    const thisPositionCoordinate = $(document)
                        .find(`[data-position-guid=${connection.from}]`)
                        .offset();
                    const foundPositionCoordinate = $(document)
                        .find(`[data-position-guid=${connection.to}]`)
                        .offset();

                    const foundPositionWidth = $(document)
                        .find(`[data-position-guid=${connection.to}]`)
                        .width();

                    if (!!foundPositionCoordinate) {
                        newCoordinates.push({
                            guid: connection.from,
                            height: foundPositionCoordinate!.top - thisPositionCoordinate!.top,
                            width: foundPositionCoordinate!.left - thisPositionCoordinate!.left,
                            type: connection.type,
                            positionWidth: foundPositionWidth!,
                            connection: connection,
                        });
                    }
                }
            }
            setCoordinates(newCoordinates);
        }
    }, [
        position,
        isOpenCharters,
        settings.isShowMHD,
        isPlanFact,
        needMoveUpdate,
        needResizeUpdate,
    ]);

    if (!position) {
        return null;
    }

    const isActive = (positionCircle: string): boolean => {
        return (
            connection.first.guid === `${position.guid}` &&
            positionCircle === connection.first.position
        );
    };

    const isShowLeft = () => {
        if (!connection.isConnection) {
            return true;
        } else {
            if (isActive('left')) {
                return true;
            } else {
                if (connection.first.type === 0) {
                    return true;
                }
            }
        }
    };
    const isShowRight = () => {
        if (!connection.isConnection) {
            return true;
        } else {
            if (isActive('right')) {
                return true;
            } else {
                if (connection.first.type === 1) {
                    return true;
                }
            }
        }
    };

    return (
        <>
            {isPlanFact ? (
                <PlanFactPositionItem
                    position={position}
                    positionX={positionX}
                    chainPositionMove={chainPositionMove}
                    coordinates={coordinates}
                    elWidth={elWidth}
                />
            ) : (
                <div className={classNames(cls.wrapper)}>
                    {!!position.connections.length &&
                        coordinates.map((coordinate, index) => (
                            <ConnectionArrow
                                type={coordinate.type}
                                key={coordinate.connection.from + coordinate.connection.to + index}
                                left={elWidth}
                                positionX={positionX + chainPositionMove.positionX}
                                height={coordinate.height}
                                width={coordinate.width}
                                positionWidth={coordinate.positionWidth}
                                connection={coordinate.connection}
                            />
                        ))}
                    <div
                        data-position-guid={position.guid}
                        style={{ left: positionX + chainPositionMove.positionX }}
                        className={classNames(cls.StageItem, { [cls.planFact]: isPlanFact })}
                    >
                        <ItemMover onMove={handleMouseMove} onMouseUp={handleMouseUp}>
                            <div
                                title={position?.name}
                                style={{ width: elWidth, minWidth: getMinWidth(activeTab) }}
                                className={classNames(cls.MainElement, [cls[position.type]], {
                                    [cls.backgroundFact]: isPlanFact,
                                })}
                            >
                                <span className={cls.text}>{position?.name}</span>

                                <ItemResizer
                                    onStageChange={onItemChange}
                                    onResize={changeWidth}
                                    elWidth={elWidth}
                                    classes={cls.resizer}
                                />
                            </div>
                        </ItemMover>
                        {isShowLeft() && (
                            <PositionCircle
                                classes={[cls.connectionLeft, isActive('left') ? cls.active : '']}
                                position={position!}
                                positionCircle={'left'}
                                widthPosition={elWidth}
                            />
                        )}
                        {isShowRight() && (
                            <PositionCircle
                                classes={[cls.connectionRight, isActive('right') ? cls.active : '']}
                                position={position!}
                                positionCircle={'right'}
                                widthPosition={elWidth}
                            />
                        )}
                    </div>
                </div>
            )}
        </>
    );
};
