import React, { FC, memo } from 'react';
import cls from './Table.module.css';
import classNames from 'classnames';
import { TableHeader } from './TableHeader/TableHeader';
import { StageItem } from './StageItem/StageItem';
import { Stage } from '../../../../store/reducers/stageSlice/stageSlice.types';
import { useSelector } from 'react-redux';
import { positionSelector } from '../../../../store/reducers/positionSlice/positionSlice';
import { charterSelector } from '../../../../store/reducers/charterSlice/charterSlice';

interface TableProps {
    stages: Stage[];
}
export const Table: FC<TableProps> = memo(({ stages }) => {
    return (
        <div className={classNames(cls.Table)}>
            <TableHeader />
            {stages.map((stage) => (
                <StageItem key={stage.id} stage={stage} />
            ))}
        </div>
    );
});
