import React, { FC } from 'react';
import cls from './MonthModeBody.module.css';
import classNames from 'classnames';
import { Stage } from '../../../../../../store/reducers/stageSlice/stageSlice.types';
import { MonthModeStage } from './MonthModeStage/MonthModeStage';

interface DaysModeBodyProps {
    stages: Stage[];
}
export const MonthModeBody: FC<DaysModeBodyProps> = ({ stages }) => {
    return (
        <div className={classNames(cls.DaysModeBody)}>
            {stages.map((stage) => (
                <MonthModeStage key={stage.id} stage={stage} />
            ))}
        </div>
    );
};
