import React, { memo, useEffect, useState } from 'react';
import s from './DataInput.module.css';
import classNames from 'classnames';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-daterangepicker/daterangepicker.css';
// @ts-ignore
import DateRangePicker from 'react-bootstrap-daterangepicker';

interface DateInputProps {
    date: string | null;
    parameterName: string;
    callback?: () => void;
    newCallback?: (value: string) => void;
    newCallbackNotSingle?: (value: { start: string; end: string }) => void;
    newCallbackNotSingleCancel?: () => void;
    options: Record<string, boolean | string>;
    title?: string;
    isRequired?: boolean;
    isDisabled?: boolean;
    placeholder?: string;
    classes?: string[];
}
export const DateInput = memo(
    ({
        date,
        parameterName,
        callback,
        options,
        newCallback,
        isDisabled,
        newCallbackNotSingle,
        placeholder,
        newCallbackNotSingleCancel,
        classes,
    }: DateInputProps) => {
        const [inputValue, setInputValue] = useState(date);

        const locale = {
            format: 'DD.MM.YYYY',
            firstDay: 1,
            daysOfWeek: ['Вс', 'Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб'],
            monthNames: [
                'Январь',
                'Февраль',
                'Март',
                'Апрель',
                'Май',
                'Июнь',
                'Июль',
                'Август',
                'Сентябрь',
                'Октябрь',
                'Ноябрь',
                'Декабрь',
            ],
            cancelLabel: 'Очистить',
            applyLabel: 'Сохранить',
        };

        const initSetting = Object.assign(options, { locale });

        const inputProps = {
            defaultValue: '',
        };

        if (date) {
            // @ts-ignore
            inputProps.defaultValue = inputValue;
        }

        const handleCancel = (event: any, picker: any) => {
            if (date === '') {
                return;
            }
            picker.element.val('');
            if (callback) {
                callback();
            }
            if (newCallbackNotSingleCancel) {
                newCallbackNotSingleCancel();
            }
        };

        const handleApply = (event: any, picker: any) => {
            picker.element.val(picker.startDate.format('DD.MM.YYYY'));
            setInputValue(picker.startDate.format('DD.MM.YYYY'));
            if (newCallback) {
                newCallback(picker.startDate.format('YYYY-MM-DD'));
            }
            if (newCallbackNotSingle) {
                picker.element.val(picker.startDate.format('DD.MM.YYYY'));

                newCallbackNotSingle({
                    start: picker.startDate.format(),
                    end: picker.endDate.format(),
                });
            }
        };

        useEffect(() => {
            setInputValue(date);
        }, [date]);

        return (
            <div className={classNames(s.wrapperInput, { [s.disabled]: isDisabled })}>
                <div className={s.calendar}>
                    <DateRangePicker
                        initialSettings={initSetting}
                        onApply={handleApply}
                        onCancel={handleCancel}
                        {...inputProps}
                    >
                        {!!date ? (
                            <input
                                placeholder={placeholder ? placeholder : ''}
                                className={classNames(s.input, [classes])}
                                disabled={isDisabled}
                                data-id={parameterName}
                                type="text"
                            />
                        ) : (
                            <div className={classNames(s.svgWrapper)}>
                                <svg
                                    width="20"
                                    height="20"
                                    viewBox="0 0 20 20"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                >
                                    <path d="M16.25 5H3.25V17H16.25V5Z" stroke="#757575" />
                                    <path d="M7.25 3H5.25V7H7.25V3Z" stroke="#757575" />
                                    <path
                                        d="M16.5 8.75H3V9.25H16.5V8.75Z"
                                        stroke="#757575"
                                        strokeWidth="0.5"
                                    />
                                    <path d="M14.25 3H12.25V7H14.25V3Z" stroke="#757575" />
                                </svg>
                            </div>
                        )}
                    </DateRangePicker>
                </div>
            </div>
        );
    }
);
