import { configureStore } from '@reduxjs/toolkit';
import stageReducer from './reducers/stageSlice/stageSlice';
import charterReducer from './reducers/charterSlice/charterSlice';
import positionReducer from './reducers/positionSlice/positionSlice';
import tableWidthReducer from './reducers/tableWidthSlice/tableWidthSlice';
import connectionReducer from './reducers/connectionSlice/connectionSlice';
import gantReducer from './reducers/gantSlice/gantSlice';

const store = configureStore({
    reducer: {
        stage: stageReducer,
        charter: charterReducer,
        position: positionReducer,
        tableWidth: tableWidthReducer,
        connection: connectionReducer,
        gant: gantReducer,
    },
});
export type AppDispatch = typeof store.dispatch;
export type RootState = ReturnType<typeof store.getState>;
export default store;
