import React, { FC, memo } from 'react';
import cls from './DaysModeStage.module.css';
import classNames from 'classnames';
import { StageItem } from '../../../../../../UI/StageItem/StageItem';
import { Stage } from '../../../../../../../store/reducers/stageSlice/stageSlice.types';
import moment from 'moment/moment';

interface DayProps {
    month: {
        title: string;
        month: number;
        days: { day: number; isWeekend: boolean; date: string }[];
    };
    stage: Stage;
    isPlanFact: boolean;
    currentDay: number;
    currentMonths: number;
}
export const Day: FC<DayProps> = memo(({ month, stage, isPlanFact, currentDay, currentMonths }) => {
    const nowDate = moment().format('DD.MM.YYYY');

    return (
        <>
            {month.days.map((day) => (
                <div
                    key={day.day}
                    className={classNames(cls.day, {
                        [cls.factWrapper]: isPlanFact,
                        [cls.weekend]: day.isWeekend,
                        [cls.nowDate]: day.date === nowDate,
                    })}
                >
                    {day.day === currentDay && currentMonths === month.month && (
                        <StageItem isPlanFact={isPlanFact} stage={stage} id={`${stage.id}`} />
                    )}
                </div>
            ))}
        </>
    );
});
