import React, { FC } from 'react';
import cls from './ModeTabs.module.css';
import classNames from 'classnames';
import { useModeTabs } from './useModeTabs';

interface ModeTabsProps {}
export const ModeTabs: FC<ModeTabsProps> = () => {
    const { activeTab, onChangeActiveTab } = useModeTabs();

    return (
        <div className={classNames(cls.ModeTabs)}>
            <div
                onClick={() => onChangeActiveTab('day')}
                className={classNames(cls.day, { [cls.active]: activeTab === 'day' })}
            >
                День
            </div>
            <div
                onClick={() => onChangeActiveTab('week')}
                className={classNames(cls.week, { [cls.active]: activeTab === 'week' })}
            >
                Неделя
            </div>
            <div
                onClick={() => onChangeActiveTab('month')}
                className={classNames(cls.month, { [cls.active]: activeTab === 'month' })}
            >
                Месяц
            </div>
        </div>
    );
};
