import React from 'react';

export const useShowBlock = (
    guid: string,
    state: Record<string, boolean>,
    setIsOpen: React.Dispatch<React.SetStateAction<Record<string, boolean>>>,
    isDisabled = false
) => {
    const isShow = state[guid];

    const onShow = () => {
        if (!isDisabled) {
            const newState = { ...state, [guid]: !state[guid] };
            setIsOpen(newState);
        }
    };

    return {
        isShow,
        onShow,
    };
};
