import React, { FC, memo, useContext } from 'react';
import cls from './WeeksModePosition.module.css';
import classNames from 'classnames';
import { Context } from '../../../../../Gant';
import { Position } from '../../../../../../../store/reducers/positionSlice/positionSlice.types';
import { WeekItemPosition } from './WeekItemPosition';

interface DaysModePositionProps {
    position?: Position;
}
export const WeeksModePosition: FC<DaysModePositionProps> = memo(({ position }) => {
    const { weeks, isPlanFact } = useContext(Context);

    if (!position) {
        return null;
    }

    return (
        <div className={classNames(cls.DaysModePosition)}>
            <div className={classNames(cls.weeks)}>
                {weeks.map((months) => (
                    <div className={classNames(cls.weeks)} key={Math.random().toString(36)}>
                        {months.weeks.map((item) => (
                            <WeekItemPosition
                                position={position}
                                week={item}
                                isPlanFact={isPlanFact}
                                key={item.title + Math.random().toString(36)}
                            />
                        ))}
                    </div>
                ))}
            </div>
        </div>
    );
});
