import React, { useEffect } from 'react';
import './App.css';
import { Gant } from './components/Gant/Gant';
import { useAppDispatch, useAppSelector } from './hooks/redux';
import { actions, stageSelector } from './store/reducers/stageSlice/stageSlice';
import { stageList } from './mockStage';
import { getNormalizedCharters, getNormalizedStages } from './helpers/getNormalized';
import { charterActions } from './store/reducers/charterSlice/charterSlice';
import { positionActions } from './store/reducers/positionSlice/positionSlice';
import { Position } from './store/reducers/positionSlice/positionSlice.types';
import { gantActions } from './store/reducers/gantSlice/gantSlice';

function App() {
    const dispatch = useAppDispatch();

    useEffect(() => {
        // @ts-ignore
        const { stages, positions: positionsList } = getNormalizedStages(stageList).entities;

        const allPositionsList: Position[] = [];
        const allCharterList: Position[] = [];

        // @ts-ignore
        for (const item of Object.values(positionsList)) {
            if (item.is_parent) {
                allCharterList.push(item);
            } else {
                allPositionsList.push(item);
            }
        }

        // @ts-ignore
        const { charters, children } = getNormalizedCharters(allCharterList).entities;
        // @ts-ignore
        const laborCount = [...allPositionsList, ...Object.values(children)].filter(
            (position) => position.type === 'labor'
        );

        dispatch(gantActions.setLaborCount(laborCount.length));
        // @ts-ignore
        dispatch(actions.addStages(Object.values(stages)));
        // @ts-ignore
        dispatch(charterActions.addStages(Object.values(charters)));
        // @ts-ignore
        dispatch(positionActions.addStages([...allPositionsList, ...Object.values(children)]));
    }, [dispatch]);

    const stages = useAppSelector(stageSelector.selectAll);

    return (
        <div className="App">
            <Gant stages={stages} />
        </div>
    );
}

export default App;
