import React, { CSSProperties, FC, useContext, useEffect, useMemo, useState } from 'react';
import cls from './Body.module.css';
import classNames from 'classnames';
import { Table } from './Table/Table';
import { Diagram } from './Diagram/Diagram';
import { Stage } from '../../../store/reducers/stageSlice/stageSlice.types';
import { Resizer } from './Resizer/Resizer';
import { Popup } from '../../UI/Popup/Popup';
import { Context } from '../Gant';
import { useUpdateAll } from '../../../hooks/useUpdateAll';
import { useSelector } from 'react-redux';
import { positionSelector } from '../../../store/reducers/positionSlice/positionSlice';
import { getGantData } from '../../../store/reducers/gantSlice/selectors/getGantData';

interface BodyProps {
    stages: Stage[];
}
export const Body: FC<BodyProps> = ({ stages }) => {
    // const width = useSelector(getTableWidth);

    const { settings } = useSelector(getGantData);

    const positionsEntities = useSelector(positionSelector.selectEntities);

    const { updateAll } = useUpdateAll();

    useEffect(() => {
        updateAll(positionsEntities);
    }, [settings.isShowMHD]);

    const [width, setWidth] = useState(480);

    const styles = useMemo<CSSProperties>(
        () => ({
            gridTemplateColumns: `${width}px 4px 1fr`,
        }),
        [width]
    );

    const { isShowPopup, setIsShowPopup } = useContext(Context);

    const handleResize = (newWidth: number) => {
        setWidth(newWidth - 21);
    };

    return (
        <div style={styles} className={classNames(cls.Body)}>
            <Popup
                isShow={isShowPopup}
                text={
                    'Длительность раздела не может быть меньше количества последовательно связанных позиций в нем. Для уменьшения его длительности измените связи между позициями'
                }
                onHide={() => setIsShowPopup(false)}
            />
            <Table stages={stages} />
            <Resizer onResize={handleResize} />
            <Diagram stages={stages} />
        </div>
    );
};
