import { createSlice, createEntityAdapter } from '@reduxjs/toolkit';
import { RootState } from '../../store';
import { Stage } from './stageSlice.types';

const stageAdapter = createEntityAdapter<Stage>({
    selectId: (payment) => payment.id,
});

const initialState = stageAdapter.getInitialState();

const stageSlice = createSlice({
    name: 'stage',
    initialState,
    reducers: {
        addStages: stageAdapter.setMany,
        updateOne: stageAdapter.updateOne,
        updateMany: stageAdapter.updateMany,
    },
});

export const stageSelector = stageAdapter.getSelectors((state: RootState) => state.stage);
export const { actions } = stageSlice;
export default stageSlice.reducer;
