import { Position } from '../store/reducers/positionSlice/positionSlice.types';
import { Dictionary } from '@reduxjs/toolkit';

export const getChainConnection = (
    position: Position,
    allPosition: Dictionary<Position>
): Position[] => {
    let result: Position[] = [];

    let connections = position.connections.filter((connect) => connect.to !== position.guid);

    if (!connections.length) {
        return result;
    }

    connections.forEach((connection) => {
        result = [...result, allPosition[connection.to]!];
        const resultRec = getChainConnection(allPosition[connection.to]!, allPosition);
        result = [...result, ...resultRec];
    });

    return result;
};
