import React, { FC } from 'react';
import cls from './WeeksModeCharter.module.css';
import classNames from 'classnames';
import { Charter } from '../../../../../../../store/reducers/charterSlice/charterSlice.types';
import moment from 'moment/moment';
import { CharterItem } from '../../../../../../UI/CharterItem/CharterItem';

interface WeekItemCharterProps {
    week: { title: string; days: string[]; date: string };
    isPlanFact: boolean;
    charter: Charter;
}
export const WeekItemCharter: FC<WeekItemCharterProps> = ({ charter, week, isPlanFact }) => {
    const charterDate = moment(charter.planned.start_date).format('DD.MM.YYYY');
    const nowDate = moment().format('DD.MM.YYYY');

    const isRenderDays =
        week.date === moment(charter.planned.start_date).format('MM.YYYY') ||
        week.date === moment().format('MM.YYYY');

    return (
        <div className={classNames(cls.week, { [cls.factWrapper]: isPlanFact })}>
            {isRenderDays &&
                week.days.map((day) => (
                    <div
                        key={day + Math.random().toString(36)}
                        data-id={day}
                        className={classNames(cls.day, {
                            [cls.factWrapper]: isPlanFact,
                            [cls.nowDate]: day === nowDate,
                        })}
                    >
                        {charterDate === day && (
                            <CharterItem isPlanFact={isPlanFact} charter={charter} />
                        )}
                    </div>
                ))}
        </div>
    );
};
