export const getWidth = (day: number, mode: 'day' | 'week' | 'month') => {
    switch (mode) {
        case 'day':
            return (day - 1) * 24 + 1;
        case 'week':
            return (day - 1) * 11 - 12;
        case 'month':
            return (day - 1) * 7 - 16;
        default:
            return (day - 1) * 24 + 1;
    }
};

export const getWidthPosition = (day: number, mode: 'day' | 'week' | 'month') => {
    switch (mode) {
        case 'day':
            return (day - 1) * 24 + 23;
        case 'week':
            return (day - 1) * 11 + 10;
        case 'month':
            return (day - 1) * 7 + 6;
        default:
            return (day - 1) * 24 + 1;
    }
};
