                    <path
                        d="M0 4C0 1.79086 1.79086 0 4 0H8C10.2091 0 12 1.79086 12 4V20C12 22.2091 10.2091 24 8 24H4C1.79086 24 0 22.2091 0 20V4Z"
                        fill={charter.done_percent >= 100 ? ' #FFC62E' : '#FFF0D5'}
                    />
                    <path
                        d="M0 4H2C2.072 2.7417 2.44736 1.70028 4 1.7141H8C9.50471 1.82027 9.85193 2.81942 10 4V20C9.99197 21.2108 9.51051 22.1587 8 22.2833H4C2.50071 22.3623 2.07768 21.1245 2 20H0C0 22.3 1.7 23.9992 4 24L8 23.9974C10.3 23.9992 12 22.3 12 20V4C12 1.7 10.3 -0.00334293 8 5.75934e-06H4C1.7 -0.000479841 0 1.7 0 4Z"
