import {
    Connections,
    Planned,
    Position,
} from '../store/reducers/positionSlice/positionSlice.types';
import { Dictionary } from '@reduxjs/toolkit';
import moment from 'moment/moment';
import { getLag } from './getLag';

export const updateChain = (
    chain: Position[],
    resultPositions: Dictionary<Position>,
    planned: Planned,
    difference: number,
    allPositions: Dictionary<Position>,
    updateConnectionPositions: (
        position: Position,
        allPosition?: Dictionary<Position>
    ) => Dictionary<Position>
): Dictionary<Position> => {
    let result: Dictionary<Position> = { ...resultPositions };
    const sortedChain = [...chain]
        .sort(
            (positionA, positionB) =>
                moment(positionA.planned.end_date!).valueOf() -
                moment(positionB.planned.end_date!).valueOf()
        )
        .reverse();

    const startChain = sortedChain.at(-1)!.planned.start_date!;
    const endChain = sortedChain[0].planned.end_date!;

    const duration = moment.duration(moment(planned.end_date!).diff(moment(endChain)));
    const daysDiff = Math.round(+duration.asDays());

    let differenceRemainder = daysDiff - difference;

    if (differenceRemainder > 0) {
        return result;
    }

    differenceRemainder = Math.abs(differenceRemainder);

    const daysFromStartCharter = moment(startChain).diff(planned.start_date, 'days');

    if (daysFromStartCharter >= differenceRemainder) {
        chain.forEach((position) => {
            const planned: Planned = {
                ...position.planned,
                start_date: moment(position.planned.start_date!)
                    .add(-differenceRemainder, 'days')
                    .format(),
                end_date: moment(position.planned.end_date!)
                    .add(-differenceRemainder, 'days')
                    .format(),
            };
            result[position.guid] = { ...result[position.guid]!, planned };
        });
        differenceRemainder = 0;
    } else {
        chain.forEach((position) => {
            const planned: Planned = {
                ...position.planned,
                start_date: moment(position.planned.start_date!)
                    .add(-daysFromStartCharter, 'days')
                    .format(),
                end_date: moment(position.planned.end_date!)
                    .add(-daysFromStartCharter, 'days')
                    .format(),
            };
            result[position.guid] = { ...result[position.guid]!, planned };
        });
        differenceRemainder -= daysFromStartCharter;
    }

    let lags: { lag: number; connection: Connections }[] = [];

    sortedChain.forEach((position, i) => {
        position.connections.forEach((connection) => {
            if (connection.to === position.guid && i !== sortedChain.length - 1) {
                const positionFrom = allPositions[connection.from]!;
                const lag = getLag(
                    position.planned.start_date!,
                    moment(positionFrom.planned.start_date!)
                        .add(+positionFrom.planned.days_count! - 1, 'days')
                        .format()
                );

                if (lag > 0) {
                    lags.push({ lag: lag, connection: connection });
                }
            }
        });
    });

    for (let i = 0; i < lags.length; i++) {
        if (lags.length === 0) {
            break;
        }
        if (lags[i].lag > 0 && differenceRemainder > 0) {
            if (differenceRemainder >= lags[i].lag) {
                const updatedLag = { ...lags[i], lag: 0 };

                const startDate = moment(allPositions[lags[i].connection.to]!.planned.start_date)
                    .add(-lags[i].lag, 'days')
                    .format();

                const endDate = moment(allPositions[lags[i].connection.to]!.planned.end_date)
                    .add(-lags[i].lag, 'days')
                    .format();

                const planned: Planned = {
                    ...allPositions[lags[i].connection.to]!.planned,
                    start_date: startDate,
                    end_date: endDate,
                };
                const updatedPosition: Position = {
                    ...allPositions[lags[i].connection.to]!,
                    planned,
                };

                differenceRemainder -= lags[i].lag;

                lags[i] = updatedLag;

                result = {
                    ...result,
                    [updatedPosition.guid]: updatedPosition,
                };

                result = updateConnectionPositions(updatedPosition, result);
            } else {
                const updatedLag = { ...lags[i], lag: lags[i].lag - differenceRemainder };

                const startDate = moment(allPositions[lags[i].connection.to]!.planned.start_date)
                    .add(-differenceRemainder, 'days')
                    .format();

                const endDate = moment(allPositions[lags[i].connection.to]!.planned.end_date)
                    .add(-differenceRemainder, 'days')
                    .format();

                const planned: Planned = {
                    ...allPositions[lags[i].connection.to]!.planned,
                    start_date: startDate,
                    end_date: endDate,
                };

                const updatedPosition = {
                    ...allPositions[lags[i].connection.to]!,
                    planned,
                };

                lags[i] = updatedLag;
                differenceRemainder = 0;

                result = {
                    ...result,
                    [updatedPosition.guid]: updatedPosition,
                };

                result = updateConnectionPositions(updatedPosition, result);
            }
        }
    }

    const updatedChain = sortedChain.map((position) => result[position.guid]!);

    updatedChain.forEach((position) => {
        if (differenceRemainder === 0) {
            return;
        }

        if (+position.planned.days_count! > 1) {
            if (+position.planned.days_count! - 1 <= differenceRemainder) {
                const endDate = position.planned.start_date;

                const planned: Planned = {
                    ...position.planned,
                    days_count: '1',
                    end_date: endDate,
                };
                const updatedPosition = { ...position, planned };

                differenceRemainder -= Number(position.planned.days_count!) - 1;

                result = {
                    ...result,
                    [updatedPosition.guid]: updatedPosition,
                };

                result = updateConnectionPositions(updatedPosition, result);
            } else {
                const countDays = +position.planned.days_count! - differenceRemainder;

                const endDate = moment(position.planned.start_date!)
                    .add(countDays - 1, 'days')
                    .format();
                const planned: Planned = {
                    ...position.planned,
                    days_count: `${countDays}`,
                    end_date: endDate,
                };

                const updatedPosition = { ...position, planned };

                differenceRemainder = 0;

                result = {
                    ...result,
                    [updatedPosition.guid]: updatedPosition,
                };

                result = updateConnectionPositions(updatedPosition, result);
            }
        }
    });

    return result;
};
