import React, { FC, memo, useContext } from 'react';
import cls from './MonthModeStage.module.css';
import classNames from 'classnames';
import { Stage } from '../../../../../../../store/reducers/stageSlice/stageSlice.types';
import { Context } from '../../../../../Gant';
import { MonthItem } from './MonthItem';
import { MonthsModeStageChild } from './MonthsModeStageChild';

interface DaysModeStageProps {
    stage: Stage;
}
export const MonthModeStage: FC<DaysModeStageProps> = memo(({ stage }) => {
    const { isPlanFact, isOpenStages, months } = useContext(Context);

    let isPlanFactShow = true;

    if (isPlanFact) {
        isPlanFactShow = isPlanFact && !!stage!.planned.start_date;
    }

    if (!isPlanFactShow) {
        return null;
    }

    return (
        <div className={classNames(cls.DaysModeStage)}>
            <div className={classNames(cls.months)}>
                {months.map((year, index) => (
                    <div className={classNames(cls.months)} key={index}>
                        {year.month.map((item) => (
                            <MonthItem
                                stage={stage}
                                key={item.date + year.title}
                                isPlanFact={isPlanFact}
                                month={item}
                            />
                        ))}
                    </div>
                ))}
            </div>
            {isOpenStages[stage.id] && (
                <>
                    {stage.positions.map((positionGuid) => (
                        <MonthsModeStageChild key={positionGuid} childGuid={positionGuid} />
                    ))}
                </>
            )}
        </div>
    );
});
