import React, { FC, useContext } from 'react';
import cls from './Header.module.css';
import classNames from 'classnames';
import { Button, ThemeButton } from '../../UI/Button/Button';
import { HeaderInterface } from './HeaderInterface/HeaderInterface';
import { useHeader } from './useHeader';
import { Popup } from '../../UI/Popup/Popup';
import { AutoPlanningModal } from '../../Modals/AutoPlanningModal/AutoPlanningModal';
import { Context } from '../Gant';

interface HeaderProps {}
export const Header: FC<HeaderProps> = () => {
    const {
        autoPlanning,
        isShowPopup,
        hidePopup,
        isShowModal,
        hideModal,
        setAutOPlanningForm,
        autOPlanningForm,
        autoPlanningMhd,
    } = useHeader();

    const { isPlanFact } = useContext(Context);

    return (
        <div className={classNames(cls.Header)}>
            <Popup
                isShow={isShowPopup}
                text={
                    'В смете нет незапланированных материалов, механизмов или доставок для автопланирования.'
                }
                onHide={hidePopup}
            />
            <AutoPlanningModal
                isOpen={isShowModal}
                onShowChange={hideModal}
                autOPlanningForm={autOPlanningForm}
                setAutOPlanningForm={setAutOPlanningForm}
                autoPlanningMhd={autoPlanningMhd}
            />
            {isPlanFact ? (
                <div />
            ) : (
                <Button
                    onClick={autoPlanning}
                    theme={ThemeButton.LINK}
                    style={{ marginLeft: '-12px' }}
                >
                    <svg
                        width="16"
                        height="16"
                        viewBox="0 0 16 16"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                    >
                        <path
                            d="M8 2.66797V13.3346V2.66797ZM8 13.3346L12 9.33463L8 13.3346ZM8 13.3346L4 9.33463L8 13.3346Z"
                            fill="#1094F7"
                        />
                        <path
                            d="M8 2.66797V13.3346M8 13.3346L12 9.33464M8 13.3346L4 9.33464"
                            stroke="#1094F7"
                            strokeWidth="1.5"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                        />
                    </svg>

                    <span className={classNames(cls.buttonText)}>Запланировать закупки</span>
                </Button>
            )}
            <HeaderInterface />
        </div>
    );
};
