import moment from 'moment';
import { isWeekend } from './isWeekend';

const monthMapping = {
    '01': 'Январь',
    '02': 'Февраль',
    '03': 'Март',
    '04': 'Апрель',
    '05': 'Май',
    '06': 'Июнь',
    '07': 'Июль',
    '08': 'Август',
    '09': 'Сентябрь',
    '10': 'Октябрь',
    '11': 'Ноябрь',
    '12': 'Декабрь',
};

export const getDays = (currentMonth: string) => {
    const result = [];

    let i = -1;
    while (i < 5) {
        const newMomentMonth = moment(currentMonth, 'MM.YYYY').add(i, 'month').format('MM.YYYY');

        const [month, year] = newMomentMonth.split('.');

        const newMonth = {
            //@ts-ignore
            title: `${monthMapping[month]} ${year}`,
            month: Number(month),
            days: [...Array(moment(newMomentMonth, 'MM.YYYY').daysInMonth())].map((_, i) => ({
                day: i + 1,
                date: moment(`${i + 1}.${month}.${year}`, 'DD.MM.YYYY').format('DD.MM.YYYY'),
                isWeekend: isWeekend(`${i + 1}.${newMomentMonth}`),
            })),
        };
        result.push(newMonth);
        i++;
    }
    return result;
};

export const getMonths = (currentMonth: string) => {
    const result = [];

    let i = -1;

    const sortedMonths = Object.entries(monthMapping).sort((a, b) => Number(a[0]) - Number(b[0]));

    while (i < 2) {
        const newMomentYear = moment(currentMonth, 'MM.YYYY').add(i, 'year').format('MM.YYYY');
        const [month, year] = newMomentYear.split('.');

        const newYear = {
            title: year,
            month: sortedMonths.map(([monthsNum, title]) => {
                return {
                    title: title,
                    date: moment(`${monthsNum}.${year}`, 'MM.YYYY').format('MM.YYYY'),
                    dayInMonth: moment(`${monthsNum}.${year}`, 'MM.YYYY').daysInMonth(),
                    days: [...Array(moment(`${monthsNum}.${year}`, 'MM.YYYY').daysInMonth())].map(
                        (_, i) => ({
                            date: moment(`${i + 1}.${monthsNum}.${year}`, 'DD.MM.YYYY').format(
                                'DD.MM.YYYY'
                            ),
                        })
                    ),
                };
            }),
        };
        result.push(newYear);
        i++;
    }
    return result;
};

const getWeeksInMonth = (month: string, year: string) => {
    moment.updateLocale('en', {
        week: {
            dow: 1, // Понедельник - первый день недели (0 - воскресенье)
        },
    });
    let weeks = [];
    let firstDay = moment([year, +month - 1]);
    let lastDay = moment(firstDay).endOf('month');
    let currentWeek = firstDay.clone().startOf('week');

    if (currentWeek.date() > 7) {
        currentWeek.add(7, 'days');
    }

    while (currentWeek < lastDay) {
        let week = {
            start: currentWeek.clone().toDate(),
            end: currentWeek.clone().endOf('week').toDate(),
        };
        weeks.push(week);

        currentWeek.add(1, 'week');
    }

    return weeks;
};

const getDatesInRange = (startDate: Date, endDate: Date) => {
    let dates = [];
    let currentDate = moment(startDate);

    while (currentDate <= moment(endDate)) {
        dates.push(currentDate.format('DD.MM.YYYY'));
        currentDate.add(1, 'days');
    }

    return dates;
};

export const getWeeks = (currentMonth: string) => {
    const result = [];

    let i = -2;
    while (i < 7) {
        const newMomentMonth = moment(currentMonth, 'MM.YYYY').add(i, 'month').format('MM.YYYY');

        const [month, year] = newMomentMonth.split('.');
        const weeks = getWeeksInMonth(month, year);

        const newWeek = {
            //@ts-ignore
            title: `${monthMapping[month]} ${year}`,
            month: Number(month),
            date: newMomentMonth,
            weeks: weeks.map((week) => ({
                title: `${moment(week.start).format('DD')} - ${moment(week.end).format('DD')}`,
                days: getDatesInRange(week.start, week.end),
                date: newMomentMonth,
            })),
        };
        result.push(newWeek);
        i++;
    }
    return result;
};

