import React, { FC, memo, useContext } from 'react';
import cls from './DaysModePosition.module.css';
import classNames from 'classnames';
import { Context } from '../../../../../Gant';
import { Position } from '../../../../../../../store/reducers/positionSlice/positionSlice.types';
import moment from 'moment';
import { DayItemPosition } from './DayItemPosition';

interface DaysModePositionProps {
    position?: Position;
}
export const DaysModePosition: FC<DaysModePositionProps> = memo(({ position }) => {
    const { isPlanFact, days } = useContext(Context);
    const currentMonths = +moment(position?.planned.start_date).locale('ru').format('MM');
    const currentDay = +moment(position?.planned.start_date).locale('ru').format('DD');

    if (!position) {
        return null;
    }

    return (
        <div className={classNames(cls.DaysModePosition)}>
            <div className={classNames(cls.days)}>
                {days.map((month, index) => (
                    <DayItemPosition
                        month={month}
                        position={position}
                        isPlanFact={isPlanFact}
                        currentDay={currentDay}
                        currentMonths={currentMonths}
                        key={index}
                    />
                ))}
            </div>
        </div>
    );
});
