import React, { FC } from 'react';
import cls from './Switcher.module.css';
import classNames from 'classnames';

interface SwitcherProps {
    title: String
    isActive: Boolean
    isDisabled?: Boolean
    onClick?: ()=>void
}

export const Switcher: FC<SwitcherProps> = ({ title, isActive, onClick, isDisabled = false }) => {
    return (
        <div className={classNames(cls.wrapper)}>
            <span className={classNames(cls.title,{ [cls.disabled]: isDisabled })}>{title}</span>
            <div
                onClick={isDisabled ? () => {} : onClick}
                className={classNames(cls.customSwitcher, {
                    [cls.active]: isActive,
                    [cls.disabled]: isDisabled,
                })}
            >
                <input type="checkbox" name="switcher" />
                <div className={classNames(cls.sliderSwitcher)} />
            </div>
        </div>
    );
};
