import React, { FC, useContext, useEffect, useState } from 'react';
import cls from './StageItem.module.css';
import classNames from 'classnames';
import { ArrowBlock } from '../../../../UI/ArrowBlock/ArrowBlock';
import { useShowBlock } from '../../../../../hooks/useShowBlock';
import { Stage } from '../../../../../store/reducers/stageSlice/stageSlice.types';
import { Context } from '../../../Gant';
import { useSelector } from 'react-redux';
import { DayInput } from '../../../../UI/DayInput/DayInput';
import { DateInput } from '../../../../UI/DateInput/DateInput';
import moment from 'moment';
import { StageChildItem } from './StageChildItem/StageChildItem';
import { useEditStage } from '../../../../../hooks/useEditStage';
import { Position } from '../../../../../store/reducers/positionSlice/positionSlice.types';
import { charterSelector } from '../../../../../store/reducers/charterSlice/charterSlice';
import { positionSelector } from '../../../../../store/reducers/positionSlice/positionSlice';
import s from '../../../../UI/DateInput/DataInput.module.css';
import { Popup } from '../../../../UI/Popup/Popup';
import { getGantData } from '../../../../../store/reducers/gantSlice/selectors/getGantData';
import { useUpdateAll } from '../../../../../hooks/useUpdateAll';

interface StageItemProps {
    stage: Stage;
}
export const StageItem: FC<StageItemProps> = ({ stage }) => {
    const { isPlanFact, setIsOpenStages, isOpenStages } = useContext(Context);
    const { resetStage, createStage, increaseStage, decreaseStage, onDataPickerStage } =
        useEditStage(stage);
    const allPositions = useSelector(positionSelector.selectEntities);
    const allCharters = useSelector(charterSelector.selectEntities);
    const { settings } = useSelector(getGantData);
    const [stageForm, setStageForm] = useState<{ days: string; date: string | null }>({
        date: stage.planned.start_date ? moment(stage.planned.start_date).format() : null,
        days: stage.planned.days_count ? stage.planned.days_count : '',
    });

    const { getStageChildren } = useUpdateAll(stage);

    const [isShowPopup, setState] = useState(false);

    const onChangePopup = (state: boolean, e: any) => {
        e.stopPropagation();
        if (isPlanFact) {
            setState(state);
        }
    };

    const { allPositions: allStagePosition } = getStageChildren(allPositions, stage);

    const stagePositions: Position[] = [];
    stage.positions.forEach((stageChildGuid) => {
        if (!!allPositions[stageChildGuid]) {
            stagePositions.push(allPositions[stageChildGuid]!);
        } else {
            allCharters[stageChildGuid]!.children.forEach((positionGuid) => {
                stagePositions.push(allPositions[positionGuid]!);
            });
        }
    });

    const isNotNeedInitStage = stagePositions.some(
        (position) => position.planned.start_date !== null
    );

    const pickerEndDate = stageForm.date
        ? moment(stageForm.date!)
              .add(+stageForm.days - 1, 'days')
              .format('DD.MM.YYYY')
        : moment(Date.now()).format('DD.MM.YYYY');

    useEffect(() => {
        setStageForm({
            date: stage.planned.start_date ? moment(stage.planned.start_date).format() : null,
            days: stage.planned.days_count ? stage.planned.days_count : '',
        });
    }, [stage]);

    let isDisabled = false;

    if (settings.isShowMHD) {
        isDisabled = !allStagePosition.length;
    } else {
        const laborPositions = allStagePosition.filter((position) => position.type === 'labor');

        isDisabled = !laborPositions.length;
    }

    const { isShow, onShow } = useShowBlock(
        `${stage.id}`,
        isOpenStages,
        setIsOpenStages,
        isDisabled
    );

    const onDateChange = (value: string) => {
        setStageForm((prevState) => ({ ...stageForm, days: value }));
    };

    const onSubmit = async () => {
        if (stageForm.days === '0' || !stageForm.days) {
            resetStage(stage);
            return;
        }

        if (!isNotNeedInitStage) {
            createStage(stage, stageForm);
        } else {
            const newEndDate = moment(stageForm.date).add(+stageForm.days - 1, 'days');
            const oldEndDate = moment(stage.planned.end_date);
            const isDecrease = newEndDate.isBefore(oldEndDate);
            const differenceInDays = Math.abs(oldEndDate.diff(newEndDate, 'days'));

            if (isDecrease) {
                decreaseStage(stage, differenceInDays);
            } else {
                increaseStage(
                    stage,
                    {
                        days: stageForm.days,
                        date: moment(stageForm.date).format(),
                    },
                    differenceInDays
                );
            }
        }
    };

    const onPaymentDateChangeCancel = () => {
        resetStage(stage);
    };
    const onPaymentDateChange = ({ start, end }: { start: string; end: string }) => {
        const startDate = moment(start);
        const endDate = moment(end);
        const duration = moment.duration(endDate.diff(startDate));
        const days = `${Math.round(+duration.asDays())}`;

        // const oldEndDate = moment(stage.planned.end_date);
        // const isDecrease = endDate.isBefore(oldEndDate);
        // const differenceInDays = Math.abs(oldEndDate.diff(endDate, 'days'));

        if (!isNotNeedInitStage) {
            createStage(stage, { days: days, date: moment(start).format() });
        } else {
            onDataPickerStage(stage, { start, end, days });
            // if (isDecrease) {
            //     decreaseStage(stage, differenceInDays, { start, end, days });
            // } else {
            //     increaseStage(
            //         stage,
            //         {
            //             days: days,
            //             date: moment(start).format(),
            //         },
            //         differenceInDays,
            //         { start, end, days }
            //     );
            // }
        }
    };

    let isPlanFactShow = true;

    if (isPlanFact) {
        isPlanFactShow = isPlanFact && !!stage!.planned.start_date;
    }

    if (!isPlanFactShow) {
        return null;
    }

    return (
        <div className={classNames(cls.wrapper, { [cls.disabled]: isDisabled })} onClick={onShow}>
            <Popup
                isShow={isShowPopup}
                text={'Смета находится в работе, поэтому редактирование данных невозможно'}
                onHide={(e: any) => onChangePopup(false, e)}
            />
            <div className={classNames(cls.StageItem, { [cls.factWrapper]: isPlanFact })}>
                {/*<div className={cls.StageItemHover} />*/}
                <div />
                <ArrowBlock isShow={isShow} />
                <div className={classNames(cls.title)}>{stage.name}</div>
                <div className={classNames(cls.capacity)} />
                <div className={classNames(cls.dateBlock)} onClick={(e) => onChangePopup(true, e)}>
                    {isPlanFact ? (
                        <>
                            {!!stage.planned.start_date ? (
                                <span className={classNames(cls.plannedDate)}>
                                    {moment(stage.planned.start_date).format('DD.MM.YYYY')}
                                </span>
                            ) : (
                                <div className={classNames(s.svgWrapper)}>
                                    <svg
                                        width="20"
                                        height="20"
                                        viewBox="0 0 20 20"
                                        fill="none"
                                        xmlns="http://www.w3.org/2000/svg"
                                    >
                                        <path d="M16.25 5H3.25V17H16.25V5Z" stroke="#757575" />
                                        <path d="M7.25 3H5.25V7H7.25V3Z" stroke="#757575" />
                                        <path
                                            d="M16.5 8.75H3V9.25H16.5V8.75Z"
                                            stroke="#757575"
                                            strokeWidth="0.5"
                                        />
                                        <path d="M14.25 3H12.25V7H14.25V3Z" stroke="#757575" />
                                    </svg>
                                </div>
                            )}
                        </>
                    ) : (
                        <>
                            <DateInput
                                key={Math.random()}
                                isRequired={true}
                                date={stageForm.date}
                                parameterName="datePlan"
                                newCallbackNotSingle={onPaymentDateChange}
                                newCallbackNotSingleCancel={onPaymentDateChangeCancel}
                                options={{
                                    startDate: stageForm.date
                                        ? moment(stageForm.date).format('DD.MM.YYYY')
                                        : moment(Date.now()).format('DD.MM.YYYY'),
                                    endDate: pickerEndDate,
                                    autoUpdateInput: true,
                                    singleDatePicker: false,
                                    showDropdowns: true,
                                    autoApply: false,
                                }}
                                classes={[cls.stageInput, cls.input]}
                            />
                        </>
                    )}

                    {isPlanFact && stage.planned.start_date !== stage.actual.start_date && (
                        <span className={classNames(cls.fact)}>
                            {moment(stage.actual.start_date).format('DD.MM.YYYY')}
                        </span>
                    )}
                </div>
                <div className={classNames(cls.daysBlock)} onClick={(e) => onChangePopup(true, e)}>
                    <>
                        {isPlanFact ? (
                            <>
                                {stage.planned.days_count ? (
                                    <span>{stage.planned.days_count}</span>
                                ) : (
                                    <span style={{ color: '#ADB0B5' }}>0</span>
                                )}
                            </>
                        ) : (
                            <DayInput
                                classes={cls.stageInput}
                                value={stageForm.days}
                                onChange={onDateChange}
                                onSubmit={onSubmit}
                            />
                        )}

                        {isPlanFact && stage.planned.days_count !== stage.actual.days_count && (
                            <span className={classNames(cls.fact)}>{stage.actual.days_count}</span>
                        )}
                    </>
                </div>
            </div>
            {isShow && (
                <div
                    className={classNames(cls.charterBlock)}
                    onClick={(event) => event.stopPropagation()}
                >
                    {stage?.positions?.map((childGuid) => (
                        <StageChildItem key={childGuid} childGuid={childGuid} />
                    ))}
                </div>
            )}
        </div>
    );
};
