import React, { FC, useContext } from 'react';
import cls from './CharterItem.module.css';
import classNames from 'classnames';
import { Context } from '../../Gant/Gant';
import { Charter } from '../../../store/reducers/charterSlice/charterSlice.types';
import { ItemResizer } from '../ItemResizer/ItemResizer';
import { useResize } from '../../../hooks/useResize';
import { ItemMover } from '../ItemMover/ItemMover';
import { useMoveCharter } from '../../../hooks/useMoveCharter';
import { PlanFactCharterItem } from './PlanFactCharterItem';

interface StageItemProps {
    charter?: Charter;
    isPlanFact: boolean;
}
export const CharterItem: FC<StageItemProps> = ({ charter, isPlanFact }) => {
    const { activeTab } = useContext(Context);

    const { positionX, handleMouseMove, handleMouseUp } = useMoveCharter(charter!, activeTab);

    const { elWidth, changeWidth, onItemChange } = useResize(activeTab, charter!);

    if (!charter) {
        return null;
    }
    const isSmall = elWidth < 97;

    return (
        <>
            {isPlanFact ? (
                <PlanFactCharterItem charter={charter} />
            ) : (
                <div className={classNames(cls.wrapper)}>
                    <div
                        style={{ left: positionX }}
                        className={classNames(cls.charterItem, { [cls.planFact]: isPlanFact })}
                    >
                        <svg
                            width="12"
                            height="24"
                            viewBox="0 0 12 24"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                        >
                            <rect width="12" height="24" rx="4" fill="#E6E6E6" />
                            <path
                                d="M12 4H10C9.928 2.7417 9.55264 1.70028 8 1.7141H4C2.49529 1.82027 2.14807 2.81942 2 4V20C2.00803 21.2108 2.48949 22.1587 4 22.2833H8C9.49929 22.3623 9.92232 21.1245 10 20H12C12 22.3 10.3 23.9992 8 24L4 23.9974C1.7 23.9992 0 22.3 0 20V4C0 1.7 1.7 -0.00334293 4 5.75934e-06H8C10.3 -0.000479841 12 1.7 12 4Z"
                                fill="#FFC62E"
                            />
                        </svg>

                        <ItemMover onMove={handleMouseMove} onMouseUp={handleMouseUp}>
                            <div
                                title={charter.name}
                                className={classNames(cls.MainElement, {
                                    [cls.backgroundFact]: isPlanFact,
                                    [cls.small]: isSmall,
                                })}
                                style={{ width: elWidth }}
                            >
                                <span className={cls.text}>{charter?.name}</span>
                            </div>
                        </ItemMover>

                        <div className={classNames(cls.resizerCharter)}>
                            <ItemResizer
                                onResize={changeWidth}
                                elWidth={elWidth}
                                onStageChange={onItemChange}
                                classes={cls.resizer}
                            />
                            <svg
                                style={{ marginLeft: '-1px', cursor: 'ew-resize' }}
                                width="12"
                                height="24"
                                viewBox="0 0 12 24"
                                fill="none"
                                xmlns="http://www.w3.org/2000/svg"
                            >
                                <rect width="12" height="24" rx="4" fill="#E6E6E6" />
                                <path
                                    d="M0 4H2C2.072 2.7417 2.44736 1.70028 4 1.7141H8C9.50471 1.82027 9.85193 2.81942 10 4V20C9.99197 21.2108 9.51051 22.1587 8 22.2833H4C2.50071 22.3623 2.07768 21.1245 2 20H0C0 22.3 1.7 23.9992 4 24L8 23.9974C10.3 23.9992 12 22.3 12 20V4C12 1.7 10.3 -0.00334293 8 5.75934e-06H4C1.7 -0.000479841 0 1.7 0 4Z"
                                    fill="#FFC62E"
                                />
                            </svg>
                        </div>
                    </div>
                </div>
            )}
        </>
    );
};
