import React, { FC, memo, useContext } from 'react';
import cls from './WeeksModeStage.module.css';
import classNames from 'classnames';
import { Stage } from '../../../../../../../store/reducers/stageSlice/stageSlice.types';
import { Context } from '../../../../../Gant';
import { WeekItemStage } from './WeekItemStage';
import { WeekModeStageChild } from './WeekModeStageChild';

interface DaysModeStageProps {
    stage: Stage;
}
export const WeeksModeStage: FC<DaysModeStageProps> = memo(({ stage }) => {
    const { weeks, isPlanFact, isOpenStages } = useContext(Context);

    let isPlanFactShow = true;

    if (isPlanFact) {
        isPlanFactShow = isPlanFact && !!stage!.planned.start_date;
    }

    if (!isPlanFactShow) {
        return null;
    }

    return (
        <div className={classNames(cls.DaysModeStage)}>
            <div className={classNames(cls.weeks)}>
                {weeks.map((month) => (
                    <div className={classNames(cls.weeks)} key={Math.random().toString(36)}>
                        {month.weeks.map((item) => (
                            <WeekItemStage
                                week={item}
                                isPlanFact={isPlanFact}
                                stage={stage}
                                key={Math.random().toString(36)}
                            />
                        ))}
                    </div>
                ))}
            </div>
            {isOpenStages[stage.id] && (
                <>
                    {stage.positions.map((childGuid) => (
                        <WeekModeStageChild
                            key={Math.random().toString(36) + childGuid}
                            childGuid={childGuid}
                        />
                    ))}
                </>
            )}
        </div>
    );
});
