import { Position } from '../store/reducers/positionSlice/positionSlice.types';
import moment from 'moment';
import { Dictionary } from '@reduxjs/toolkit';
import { getChainConnection } from './getChainConnection';

const getChain = (
    position: Position,
    chain: Position[],
    positionWithConnection: Position[],
    lastLaborPositionGuid: string
): { chain: Position[]; positionWithConnection: Position[] } => {
    const filteredPosition = positionWithConnection.filter(({ guid }) => guid !== position.guid);

    let result = {
        chain: [...chain, position],
        positionWithConnection: [...filteredPosition],
    };

    const connectionTo = position.connections.find(
        (connection) => connection.from === position.guid
    );
    if (!connectionTo || lastLaborPositionGuid === position.guid) return result;

    const nextPosition = positionWithConnection.find(
        (position) => position.guid === connectionTo!.to
    );

    if (!nextPosition) {
        return result;
    }

    const { chain: chainResult, positionWithConnection: positionWithConnectionResult } = getChain(
        nextPosition!,
        [...chain],
        filteredPosition,
        lastLaborPositionGuid
    );

    return {
        chain: [...result.chain, ...chainResult],
        positionWithConnection: positionWithConnectionResult,
    };
};
export const getChains = (
    positionWithConnection: Position[],
    lastLaborPositionGuid: string,
    positions: Dictionary<Position> = {}
): Array<Position[]> => {
    const sortedChain = [...positionWithConnection].sort(
        (positionA, positionB) =>
            moment(positionA.planned.end_date!).valueOf() -
            moment(positionB.planned.end_date!).valueOf()
    );

    if (!sortedChain.length) {
        return [];
    }
    let result: Array<Position[]> = [];

    const chain = getChain(sortedChain[0], [], sortedChain, lastLaborPositionGuid);
    // const chain = getChainConnection(sortedChain[0], positions);

    result.push(chain.chain);
    // result.push(chain);

    result = [...result, ...getChains(chain.positionWithConnection, lastLaborPositionGuid)];

    return result;
};
