import React, { createContext, FC } from 'react';
import cls from './Gant.module.css';
import classNames from 'classnames';
import { Header } from './Header/Header';
import { useGant } from './useGant';
import { Body } from './Body/Body';
import { Stage } from '../../store/reducers/stageSlice/stageSlice.types';

export interface Days {
    title: string;
    days: {
        day: number;
        isWeekend: boolean;
    }[];
}

export interface Month {
    title: string;
    month: string[];
}

export interface IContext {
    isPlanFact: boolean;
    isShowPopup: boolean;
    setIsPlanFact: React.Dispatch<React.SetStateAction<boolean>>;
    setIsShowPopup: React.Dispatch<React.SetStateAction<boolean>>;
    activeTab: 'day' | 'week' | 'month';
    setActiveTab: React.Dispatch<React.SetStateAction<'day' | 'week' | 'month'>>;
    isOpenStages: Record<string, boolean>;
    setIsOpenStages: React.Dispatch<React.SetStateAction<Record<string, boolean>>>;
    isOpenCharters: Record<string, boolean>;
    setIsOpenCharters: React.Dispatch<React.SetStateAction<Record<string, boolean>>>;
    isOpenPositions: Record<string, boolean>;
    setIsOpenPositions: React.Dispatch<React.SetStateAction<Record<string, boolean>>>;
    weeks: {
        title: string;
        month: number;
        weeks: { date: string; title: string; days: string[] }[];
    }[];
    days: {
        title: string;
        month: number;
        days: { day: number; isWeekend: boolean; date: string }[];
    }[];
    months: {
        title: string;
        month: {
            title: string;
            dayInMonth: number;
            date: string;
            days: {
                date: string;
            }[];
        }[];
    }[];
}
export const Context = createContext<IContext>({} as IContext);

export interface GantProps {
    stages: Stage[];
}

export const Gant: FC<GantProps> = ({ stages }) => {
    const { contextValue } = useGant();
    return (
        <Context.Provider value={contextValue}>
            <div className={classNames(cls.wrapper)}>
                <Header />
                <Body stages={stages} />
            </div>
        </Context.Provider>
    );
};
