import { createSlice, createEntityAdapter, PayloadAction } from '@reduxjs/toolkit';
import { RootState } from '../../store';
import { Position } from './positionSlice.types';

const positionAdapter = createEntityAdapter<Position>({
    selectId: (payment) => payment.guid,
});

const initialState = positionAdapter.getInitialState();

const positionSlice = createSlice({
    name: 'position',
    initialState,
    reducers: {
        addStages: positionAdapter.setMany,
        updateOne: positionAdapter.updateOne,
        updateMany: positionAdapter.updateMany,
    },
});

export const positionSelector = positionAdapter.getSelectors((state: RootState) => state.position);
export const { actions: positionActions } = positionSlice;
export default positionSlice.reducer;
