import { Planned, Position } from '../store/reducers/positionSlice/positionSlice.types';
import { Dictionary } from '@reduxjs/toolkit';
import moment from 'moment';

export const updatePositionWithoutConnect = (
    position: Position,
    resultPositions: Dictionary<Position>,
    planned: Planned,
    difference: number,
    allPositions: Dictionary<Position>
): Dictionary<Position> => {
    let result: Dictionary<Position> = { ...resultPositions };

    if (!position.planned.days_count) return result;

    const duration = moment.duration(
        moment(planned.end_date!).diff(moment(position.planned.end_date))
    );
    const daysDiff = Math.round(+duration.asDays());

    let differenceRemainder = daysDiff - difference;

    if (differenceRemainder > 0) {
        return result;
    }

    differenceRemainder = Math.abs(differenceRemainder);

    const daysFromStartCharter = moment(position.planned.start_date).diff(
        planned.start_date,
        'days'
    );

    if (daysFromStartCharter >= differenceRemainder) {
        const planned: Planned = {
            ...position.planned,
            start_date: moment(position.planned.start_date!)
                .add(-differenceRemainder, 'days')
                .format(),
            end_date: moment(position.planned.end_date!).add(-differenceRemainder, 'days').format(),
        };

        result[position.guid] = { ...result[position.guid]!, planned };
        differenceRemainder = 0;
    } else {
        const planned: Planned = {
            ...position.planned,
            start_date: moment(position.planned.start_date!)
                .add(-daysFromStartCharter, 'days')
                .format(),
            end_date: moment(position.planned.end_date!)
                .add(-daysFromStartCharter, 'days')
                .format(),
        };

        result[position.guid] = { ...result[position.guid]!, planned };
        differenceRemainder -= daysFromStartCharter;
    }

    if (differenceRemainder > 0) {
        const currentPosition = result[position.guid]!;

        const planned: Planned = {
            ...currentPosition.planned,
            days_count: `${+currentPosition.planned.days_count! - differenceRemainder}`,
            end_date: moment(currentPosition.planned.end_date)
                .add(-differenceRemainder, 'days')
                .format(),
        };

        result[position.guid] = { ...currentPosition, planned };
    }

    return result;
};
