import { useState } from 'react';
import { useAutoPlanningMhd } from '../../../hooks/useAutoPlanningMhd';
import { useSelector } from 'react-redux';
import { getGantData } from '../../../store/reducers/gantSlice/selectors/getGantData';

export const useHeader = () => {
    const { settings } = useSelector(getGantData);
    const [isShowPopup, setIsShowPopup] = useState(false);
    const [isShowModal, setIsShowModal] = useState(false);
    const [autOPlanningForm, setAutOPlanningForm] = useState({
        days: `${settings.daysMHD}`,
    });

    const { isNotMHD, autoPlanningMhd } = useAutoPlanningMhd();

    const hidePopup = () => setIsShowPopup(false);
    const hideModal = () => {
        setIsShowModal(false);
    };
    const autoPlanning = () => {
        if (isNotMHD) {
            setIsShowPopup(true);
            return;
        }
        setIsShowModal(true);
    };

    return {
        autoPlanning,
        isShowPopup,
        hidePopup,
        hideModal,
        isShowModal,
        autOPlanningForm,
        setAutOPlanningForm,
        autoPlanningMhd,
    };
};
