import React, { FC, memo, useContext } from 'react';
import cls from './WeeksModeHeader.module.css';
import classNames from 'classnames';
import { Context } from '../../../../Gant';
import moment from 'moment';

interface DaysModeHeaderProps {}
export const WeeksModeHeader: FC<DaysModeHeaderProps> = memo(() => {
    const { weeks } = useContext(Context);

    const nowDate = moment().format('DD.MM.YYYY');

    return (
        <div className={classNames(cls.DaysModeHeader)}>
            {weeks.map((week) => (
                <div className={classNames(cls.yearsWrapper)} key={Math.random().toString(36)}>
                    <div className={classNames(cls.month)}>{week.title}</div>
                    <div className={classNames(cls.weeks)}>
                        {week.weeks.map((item) => (
                            <div
                                key={item.title + Math.random().toString(36)}
                                className={classNames(cls.week)}
                            >
                                <span className={cls.title}>{item.title}</span>

                                {item.days.map((day) => (
                                    <div
                                        key={day}
                                        data-date-now={day === nowDate}
                                        style={{ width: 11 }}
                                        className={classNames({
                                            [cls.nowDate]: day === nowDate,
                                        })}
                                    ></div>
                                ))}
                            </div>
                        ))}
                    </div>
                </div>
            ))}
        </div>
    );
});
