import React, { FC, memo, useContext } from 'react';
import cls from './MonthModePosition.module.css';
import classNames from 'classnames';
import { Context } from '../../../../../Gant';
import { Position } from '../../../../../../../store/reducers/positionSlice/positionSlice.types';
import { MonthItemPosition } from './MonthItemPosition';

interface DaysModePositionProps {
    position?: Position;
}
export const MonthModePosition: FC<DaysModePositionProps> = memo(({ position }) => {
    const { isPlanFact, months } = useContext(Context);

    if (!position) {
        return null;
    }

    return (
        <div className={classNames(cls.DaysModePosition)}>
            <div className={classNames(cls.months)}>
                {months.map((year, i) => (
                    <div className={classNames(cls.months)} key={year.title + i}>
                        {year.month.map((item) => (
                            <MonthItemPosition
                                month={item}
                                isPlanFact={isPlanFact}
                                position={position!}
                                key={item.date + Math.random().toString(36)}
                            />
                        ))}
                    </div>
                ))}
            </div>
        </div>
    );
});
