import React, { FC, memo, useContext, useEffect, useRef, useState } from 'react';
import cls from './Diagram.module.css';
import classNames from 'classnames';
import { Context } from '../../Gant';
import { DaysMode } from './DaysMode/DaysMode';
import { WeeksMode } from './WeeksMode/WeeksMode';
import { MonthMode } from './MonthMode/MonthMode';
import { Stage } from '../../../../store/reducers/stageSlice/stageSlice.types';
import { Arrow } from '../../../UI/Arrow/Arrow';
import { useAppSelector } from '../../../../hooks/redux';
import { getConnectionsData } from '../../../../store/reducers/connectionSlice/selectors/getConnectionsData';

interface DiagramProps {
    stages: Stage[];
}

export const Diagram: FC<DiagramProps> = memo(({ stages }) => {
    const { activeTab } = useContext(Context);
    const diagramRef = useRef<HTMLDivElement | null>(null);
    const [position, setPosition] = useState({ x: 0, y: 0 });
    const [scrollLeft, setScrollLeft] = useState(0);
    const { isConnection } = useAppSelector(getConnectionsData);

    useEffect(() => {
        if (diagramRef !== null) {
            diagramRef.current?.scroll(0, 0);

            const { x, y } = {
                x: diagramRef.current!.offsetLeft,
                y: diagramRef.current!.offsetTop,
            };
            const element = diagramRef.current?.querySelector('[data-date-now="true"]');

            let relativeCoordinates = 0;

            if (element) {
                const diagramRect = diagramRef.current!.getBoundingClientRect();
                const elementRect = element.getBoundingClientRect();

                relativeCoordinates = elementRect.left - diagramRect.left;
            }

            setPosition({ x, y });
            diagramRef.current?.scroll(relativeCoordinates - 72, 0);
        }
    }, [diagramRef, activeTab]);

    useEffect(() => {
        const handleScroll = (event: any) => {
            setScrollLeft(event.currentTarget.scrollLeft);
        };

        const currentDiagram = diagramRef.current;
        if (currentDiagram) {
            currentDiagram.addEventListener('scroll', (event) => handleScroll(event));
        }

        return () => {
            if (currentDiagram) {
                currentDiagram.removeEventListener('scroll', (event) => handleScroll(event));
            }
        };
    }, []);

    const modeMapping: Record<string, React.ReactNode> = {
        day: <DaysMode stages={stages} />,
        week: <WeeksMode stages={stages} />,
        month: <MonthMode stages={stages} />,
    };

    return (
        <div className={classNames(cls.Diagram)} ref={diagramRef}>
            {isConnection && <Arrow position={position} scrollLeft={scrollLeft} />}
            {modeMapping[activeTab]}
        </div>
    );
});
