    //         );
    //
    //     const positionsResult = { ...positionsEntities, ...positions };
    //
    //     const chartersUpdateData: { id: string; changes: Charter }[] = [];
    //
    //     charters.forEach((charter) => {
    //         const chartersChildren: Position[] = charter.children
    //             .map((guid) => positionsResult[guid]!)
    //             .filter((pos) => pos.type === 'labor')
    //             .filter((pos) => pos.planned.days_count)
    //             .sort(
    //                 (positionA, positionB) =>
    //                     moment(positionA.planned.end_date!).valueOf() -
    //                     moment(positionB.planned.end_date!).valueOf()
    //             );
    //
    //         if (chartersChildren.length) {
    //             const updatedCharter = {
    //                 ...charter,
    //                 planned: {
    //                     days_count: `${getCountDaysFromDate(
    //                         chartersChildren.at(-1)!.planned.end_date!,
    //                         chartersChildren[0].planned.start_date!
    //                     )}`,
    //                     start_date: chartersChildren[0].planned.start_date,
    //                     end_date: chartersChildren.at(-1)!.planned.end_date,
    //                 },
    //             };
    //
    //             chartersUpdateData.push({
    //                 id: charter.guid,
    //                 changes: updatedCharter,
    //             });
    //         }
    //     });
    //
    //     const updateAllPositions = allStagePositions.map((position) => ({
    //         id: position.guid,
    //         changes: position,
    //     }));
    //
    //     dispatch(positionActions.updateMany(updateAllPositions));
