    //
    //     if (sortedStagePosition.length) {
    //         stagePlanned = {
    //             days_count: `${getCountDaysFromDate(
    //                 sortedStagePosition.at(-1)!.planned.end_date!,
    //                 sortedStagePosition[0].planned.start_date!
