import { useEffect, useState } from 'react';
import { Stage } from '../store/reducers/stageSlice/stageSlice.types';
import { getCountDays } from '../helpers/getCountDays';
import moment from 'moment';
import { useEditStage } from './useEditStage';

export const useMoveStage = (stage: Stage, activeTab: 'day' | 'week' | 'month') => {
    const [positionX, setPositionX] = useState(0);
    const [needMoveUpdate, setNeedMoveUpdate] = useState(false);

    const { moveStage } = useEditStage(stage);

    const handleMouseMove = (value: number) => {
        setPositionX((prevState) => prevState + value);
    };

    const handleMouseUp = () => {
        setNeedMoveUpdate(true);
    };

    useEffect(() => {
        if (needMoveUpdate) {
            const startDate = stage!.planned.start_date ? stage!.planned.start_date : '';
            const start_date = getCountDays(positionX, startDate, activeTab);

            const dateDiff = moment(start_date).diff(startDate, 'days');

            if (dateDiff !== 0) {
                moveStage(stage, dateDiff);
            }

            setPositionX(0);
            setNeedMoveUpdate(false);
        }
    }, [needMoveUpdate]);

    return {
        positionX,
        handleMouseMove,
        handleMouseUp,
    };
};
